import { Notes, MyProblems, Notices } from "../actionTypes";
const initialState = 0;

const HomeReducer = (state = initialState, action) => {
  switch (action.type) {
    case Notes:
      return 2;
    case MyProblems:
      return 0;
    case Notices:
      return 1;
    default:
      return state;
  }
};

export default HomeReducer;
