import React, { useState } from "react";
import moment from "moment";
import { connect } from "react-redux";
import {
  IconButton,
  CircularProgress,
  ExpansionPanelActions,
  MenuItem,
  TextField,
  ExpansionPanel,
  Container,
  Button as ButtonM,
  ExpansionPanelSummary,
  Typography,
  ExpansionPanelDetails,
  Menu,
  Avatar,
  Tooltip,
} from "@material-ui/core";
import PropTypes from "prop-types";
import {
  Button,
  ModalHeader,
  Comment,
  CommentAvatar,
  PlaceholderLine,
} from "semantic-ui-react";
import url from "../../../../strings/url";

import {
  UpVote,
  RemoveUpVote,
  DownVote,
  RemoveDownVote,
  getComments,
  postComment,
  getVotesBy,
  getCommentsCount,
} from "../../../../ApiRequests/request";
import {
  ArrowUpward,
  ArrowDownward,
  Delete,
  CommentOutlined,
  ExpandMoreOutlined,
  VisibilityOffTwoTone,
} from "@material-ui/icons";
import {
  Image,
  Card,
  Divider,
  PlaceholderImage,
  Placeholder,
  Modal,
} from "semantic-ui-react";
import { blue, red } from "@material-ui/core/colors";
import { Result } from "antd";
import { Skeleton } from "@material-ui/lab";

const fontStyle = {
  fontFamily: "poppins",
  fontSize: 12,
};
const fontStyle1 = {
  fontFamily: "poppins",
  fontSize: 15,
};

const Problem = (props) => {
  const [upVotes, setUpVotes] = useState(props.UpVotes);
  const [downVotes, setDownVotes] = useState(props.DownVotes);
  const [isUpVoted, setUpVote] = useState(props.UpVotedByMe);
  const [isDownVoted, setDownVote] = useState(props.DownVotedByMe);
  const [commentSection, setCommentSection] = useState(false);
  const [commentDescription, setCommentDescription] = useState("");
  const [commentSectionLoading, setCommentSectionLoading] = useState(false);
  const [commentsFetching, setCommentsFetching] = useState(false);
  const [comments, setComments] = useState([]);
  const [isEOpen, setExpansionPanel] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const [isVotesLoading, setVotesLoading] = useState(false);
  const [votes, setVotes] = useState([]);
  const [whichVote, setWhichVote] = useState(undefined);
  const [isCommentCountFetch, setIsCommentCount] = useState(false);
  const [commentCount, setCommentCount] = useState(0);

  if (!isCommentCountFetch) {
    getCommentsCount(props.id)
      .then(async (res) => {
        if (res.ok) {
          setCommentCount((await res.json()).count);
          setIsCommentCount(true);
        } else {
          setIsCommentCount(true);
        }
      })
      .catch(() => {
        setIsCommentCount(true);
      });
  }

  const handleGetVotedBy = async (type) => {
    try {
      const response = await getVotesBy(
        localStorage.getItem("AnonymityToken#123"),
        props.id,
        type
      );

      if (response.ok) {
        const res = await response.json();
        setVotes(res);
        setVotesLoading(false);
      }
    } catch (e) {
      setVotes([]);
      setVotesLoading(false);
      console.log(e.message);
    }
  };
  const handleGetComments = async () => {
    try {
      setCommentsFetching(true);

      const res = await getComments(
        props.id,
        localStorage.getItem("AnonymityToken#123")
      );

      const rer = await res.json();

      setComments(rer);
      setCommentsFetching(false);
    } catch (e) {
      console.log(e.message);
      setCommentsFetching(false);
    }
  };
  const HandleUpVote = () => {
    if (isDownVoted) {
      setDownVote(false);
      setDownVotes((state) => {
        return state - 1;
      });
      setUpVotes((state) => {
        return state + 1;
      });
      setUpVote((state) => {
        return true;
      });
      UpVote(props.id, localStorage.getItem("AnonymityToken#123")).then(
        (res) => {
          RemoveDownVote(props.id, localStorage.getItem("AnonymityToken#123"));
        }
      );
    } else if (!isUpVoted) {
      setUpVotes((state) => {
        return state + 1;
      });
      setUpVote((state) => {
        return true;
      });
      UpVote(props.id, localStorage.getItem("AnonymityToken#123"));
    } else if (isUpVoted) {
      setUpVotes((state) => {
        return state - 1;
      });
      setUpVote((state) => {
        return false;
      });
      RemoveUpVote(props.id, localStorage.getItem("AnonymityToken#123"));
    }
  };
  const handleDownVote = () => {
    if (isUpVoted) {
      setUpVote(false);
      setUpVotes((state) => {
        return state - 1;
      });
      setDownVotes((state) => {
        return state + 1;
      });
      setDownVote(true);
      DownVote(props.id, localStorage.getItem("AnonymityToken#123")).then(
        (res) => {
          return RemoveUpVote(
            props.id,
            localStorage.getItem("AnonymityToken#123")
          );
        }
      );
    } else if (!isDownVoted) {
      setDownVotes((state) => {
        return state + 1;
      });
      setDownVote(true);
      DownVote(props.id, localStorage.getItem("AnonymityToken#123"));
    } else if (isDownVoted) {
      setDownVotes((state) => {
        return state - 1;
      });
      setDownVote(false);
      RemoveDownVote(props.id, localStorage.getItem("AnonymityToken#123"));
    }
  };
  const deletePost = async () => {
    const headers = new Headers();
    const token = localStorage.getItem("AnonymityToken#123");
    headers.append("Authorization", token);
    headers.append("content-type", "application/json");
    const request = new Request(`${url}/user/post/${props.id}`, {
      method: "DELETE",
      headers,
    });
    try {
      const response = await fetch(request);
      if (response.status === 200) {
        window.location.reload(true);
      }
      setError(true);
    } catch (e) {
      setError(true);
    }
  };
  const [isImageLoading, setImage] = useState(true);
  const [isError, setError] = useState(false);
  const [showImageModal, setModal] = useState(false);
  const [showDeleteModal, setDeleteModal] = useState(false);

  const calculateWidth = () => {
    if (props.deviceType === "comp") {
      return { width: 600 };
    }
    if (props.deviceType === "mob") {
      return { width: 325 };
    }
    if (props.deviceType === "tab") {
      return { width: 500 };
    }
    if (!props.deviceType) {
      return { width: "100%" };
    }
  };

  return (
    <Container>
      <Divider hidden></Divider>
      <Card
        color="blue"
        style={{
          ...calculateWidth(),
          border: "none",
          display: "flex",
          justifyContent: "center",
          borderRadius: 0,
          marginBottom: 10,
          boxShadow: "1px 0px 15px rgba(0,0,0,0.2)",
        }}
      >
        <Card.Content>
          <Card.Header
            style={{ color: "grey", fontFamily: "poppins", cursor: "pointer" }}
            onClick={() => {
              if (!Boolean(props.anonymous)) {
                props.history.push(`/anonymity/person/${props.UserId}`);
                props.dispatch({ type: "Person" });
              }
            }}
          >
            <Avatar sizes="large" src={props.profilePhoto}></Avatar>
            {props.username}
            {props.anonymous && props.isHome ? (
              <Tooltip title="this post is anonymous">
                <Avatar
                  style={{ float: "right" }}
                  src={"https://img.icons8.com/doodle/48/000000/fraud.png"}
                ></Avatar>
              </Tooltip>
            ) : (
              ""
            )}
          </Card.Header>
          <Card.Meta style={{ color: "grey", fontFamily: "poppins" }}>
            {"Posted " +
              moment(props.createdAt).fromNow() +
              " \n" +
              "On " +
              moment(props.createdAt).format("LL")}
          </Card.Meta>
        </Card.Content>
        {isImageLoading ? (
          <Placeholder>
            <PlaceholderImage rectangular></PlaceholderImage>
          </Placeholder>
        ) : (
          ""
        )}
        <div
          onClick={() => {
            setModal(true);
          }}
        >
          <Image
            src={props.image}
            style={
              isImageLoading || isError
                ? { width: 0, height: 0 }
                : { width: "100%" }
            }
            onLoad={() => {
              setImage(false);
            }}
            onError={() => {
              setImage(false);
              setError(true);
            }}
          ></Image>
        </div>
        <Card.Content>
          <Card.Content>
            <div
              style={{
                fontFamily: "poppins",
                fontSize: 12,
                fontWeight: "bolder",
                color: "black",
              }}
            >
              Complaint Statement
            </div>
          </Card.Content>
          <Card.Content>
            <Card.Description
              style={{
                fontWeight: "500",
                fontFamily: "poppins",
                wordWrap: "break-word",
                hyphens: "auto",
                marginTop: 10,
              }}
            >
              {props.description}
            </Card.Description>
          </Card.Content>
        </Card.Content>

        <ExpansionPanel
          TransitionProps={{ unmountOnExit: true }}
          expanded={isEOpen}
          onChange={(e, i) => {
            setExpansionPanel(i);
          }}
          elevation={0}
        >
          <ExpansionPanelSummary
            expandIcon={
              <Tooltip title={"Expand"}>
                <ExpandMoreOutlined></ExpandMoreOutlined>
              </Tooltip>
            }
          >
            <ExpansionPanelActions
              aria-label="acknowledge"
              onClick={(e) => e.stopPropagation()}
              onFocus={(e) => {
                e.stopPropagation();
              }}
            >
              {upVotes}
              <Tooltip title="UpVote">
                <IconButton
                  size="medium"
                  style={{
                    marginLeft: "0.5%",
                    marginRight: "1%",
                    color: isUpVoted ? blue[700] : "grey",
                  }}
                  onClick={() => {
                    HandleUpVote();
                  }}
                >
                  <ArrowUpward></ArrowUpward>
                </IconButton>
              </Tooltip>

              {downVotes}
              <Tooltip title="DownVote">
                <IconButton
                  size="medium"
                  style={{
                    marginLeft: "1%",
                    color: isDownVoted ? red[500] : "grey",
                  }}
                  onClick={() => {
                    handleDownVote();
                  }}
                >
                  <ArrowDownward></ArrowDownward>
                </IconButton>
              </Tooltip>
              <Tooltip title="Comments">
                <IconButton
                  onClick={() => {
                    handleGetComments();
                    setCommentSection(true);
                  }}
                >
                  <CommentOutlined></CommentOutlined>
                </IconButton>
              </Tooltip>
              {commentCount}
            </ExpansionPanelActions>
          </ExpansionPanelSummary>

          <ExpansionPanelDetails>
            <code>Status...</code>
            <VisibilityOffTwoTone></VisibilityOffTwoTone>
          </ExpansionPanelDetails>
          <ExpansionPanelActions>
            {upVotes > 0 ? (
              <ButtonM
                onClick={(e) => {
                  setAnchorEl(e.currentTarget);
                  setVotesLoading(true);
                  setWhichVote("UpVotes");
                  handleGetVotedBy("upVotedBy");
                }}
              >
                UpVoted By
              </ButtonM>
            ) : (
              ""
            )}
            {downVotes > 0 ? (
              <ButtonM
                onClick={(e) => {
                  setAnchorEl(e.currentTarget);
                  setVotesLoading(true);
                  setWhichVote("DownVotes");
                  handleGetVotedBy("downVotedBy");
                }}
              >
                DownVoted By
              </ButtonM>
            ) : (
              ""
            )}

            {props.hasDeleteButton ? (
              <ButtonM
                variant="outlined"
                onClick={() => {
                  setDeleteModal(true);
                }}
              >
                <Delete style={{ color: red[500] }}></Delete> Delete
              </ButtonM>
            ) : (
              ""
            )}
          </ExpansionPanelActions>
        </ExpansionPanel>
        <Menu
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          onClose={() => {
            setAnchorEl(null);
            setWhichVote(undefined);
            setVotes([]);
          }}
          style={{ maxHeight: 300 }}
        >
          {isVotesLoading ? (
            <div>
              <MenuItem>
                <Skeleton variant="circle" width={40} height={40}></Skeleton>
                <Skeleton variant="text" width={230} height={40}></Skeleton>
              </MenuItem>
            </div>
          ) : votes.length > 0 ? (
            votes.map(({ user }) => {
              return (
                <MenuItem
                  key={user._id}
                  button
                  onClick={() => {
                    props.dispatch({ type: "Person" });
                    props.history.push(`/anonymity/person/${user._id}`);
                    if (props.currentTab === "Person") {
                      window.location.reload(false);
                    }
                  }}
                >
                  <Avatar src={user.profilephoto}></Avatar>
                  <div
                    style={{
                      marginLeft: 10,
                      width: 200,
                      fontWeight: "bold",
                      fontFamily: "poppins",
                    }}
                  >
                    {user.firstName + " " + user.LastName}
                  </div>
                </MenuItem>
              );
            })
          ) : (
            <div
              style={{
                margin: 10,
                fontWeight: "bold",
                fontFamily: "poppins",
                width: 200,
              }}
            >
              {`No ${whichVote}`}
            </div>
          )}
        </Menu>
        <Card.Content>
          {commentSectionLoading ? (
            <CircularProgress></CircularProgress>
          ) : (
            <input
              placeholder="Comment..."
              value={commentDescription}
              onChange={(e) => {
                setCommentDescription(e.target.value);
              }}
              style={{
                width: "80%",
                border: "none",
                fontSize: 14,
                fontFamily: "poppins",
                padding: 4,
                outline: "none",
              }}
            ></input>
          )}
          <Tooltip title="Post Comment">
            <button
              disabled={commentDescription.length > 0 ? false : true}
              onClick={async () => {
                try {
                  const token = localStorage.getItem("AnonymityToken#123");
                  setCommentSectionLoading(true);
                  const res = await postComment(
                    props.id,
                    token,
                    commentDescription
                  );
                  const bdy = await res.json();
                  if (res.status === 200) {
                    setCommentCount(commentCount + 1);
                    setComments(
                      comments.concat([
                        {
                          description: commentDescription,
                          By: {
                            profilephoto: props.profilePhoto1,
                            firstName: "By",
                            LastName: "Me... Right Now",
                          },
                        },
                      ])
                    );
                    setCommentDescription("");
                  }
                  setCommentSectionLoading(false);
                } catch (e) {
                  setCommentSectionLoading(false);
                }
              }}
              style={{
                padding: 4,
                color: commentDescription.length == 0 ? blue[100] : blue[500],
                float: "right",
                background: "transparent",
                fontSize: 15,
                fontFamily: "poppins",
                border: "none",
                outline: "none",
                cursor: "pointer",
                marginRight: 10,
              }}
            >
              Post
            </button>
          </Tooltip>
        </Card.Content>
      </Card>
      <Divider hidden></Divider>
      <Modal
        open={showImageModal}
        basic
        onClose={() => {
          setModal(false);
        }}
        closeIcon
      >
        <Image src={props.image} centered></Image>
      </Modal>
      {props.hasDeleteButton ? (
        <Modal basic open={showDeleteModal}>
          <Modal.Header>Delete Your Problem</Modal.Header>
          <Modal.Content>
            <p>Are you sure you want to delete This Post ??</p>
          </Modal.Content>
          <Modal.Actions>
            <Button
              onClick={() => {
                setDeleteModal(false);
              }}
              color="red"
              inverted
            >
              No
            </Button>
            <Button
              positive
              onClick={() => {
                deletePost();
              }}
              color="green"
              inverted
            >
              Yes
            </Button>
          </Modal.Actions>
        </Modal>
      ) : (
        ""
      )}
      <Modal
        open={commentSection}
        size="tiny"
        onClose={() => {
          setCommentSection(false);
        }}
      >
        <ModalHeader
          style={{
            fontFamily: "poppins",
            backgroundColor: "rgba(0,0,0,1)",
            fontSize: 15,
          }}
        >
          <div
            style={{
              display: "flex",
              justifyContent: "flex-start",
              color: "rgba(255,255,255,0.5)",
            }}
          >
            Comments on{" "}
            {props.username == "Anonymous"
              ? "Someone's"
              : props.username + "'s"}{" "}
            Problem
          </div>
        </ModalHeader>
        <Modal.Content scrolling>
          <Comment.Group>
            {comments.length > 0 && !commentsFetching ? (
              comments.map((comment, index) => {
                return (
                  <Comment key={comment + index}>
                    <Comment.Avatar src={comment.By.profilephoto} />
                    <Comment.Content>
                      <Comment.Author
                        as="a"
                        onClick={() => {
                          props.dispatch({ type: "Person" });
                          props.history.push(
                            `/anonymity/person/${comment.By._id}`
                          );
                        }}
                        style={{
                          fontFamily: "poppins",
                        }}
                      >
                        {comment.By.firstName + " " + comment.By.LastName}
                      </Comment.Author>
                      <Comment.Metadata>
                        {comment.createdAt
                          ? moment(comment.createdAt).fromNow()
                          : ""}
                      </Comment.Metadata>
                      <Comment.Text
                        style={{
                          fontFamily: "poppins",
                        }}
                      >
                        {comment.description}
                      </Comment.Text>
                      <Comment.Actions>
                        <Comment.Action>Like</Comment.Action>
                      </Comment.Actions>
                    </Comment.Content>
                  </Comment>
                );
              })
            ) : commentsFetching ? (
              <Placeholder>
                <Placeholder.Header image>
                  <Placeholder.Line></Placeholder.Line>
                  <Placeholder.Line></Placeholder.Line>
                </Placeholder.Header>
                <Placeholder.Header image>
                  <Placeholder.Line></Placeholder.Line>
                  <Placeholder.Line></Placeholder.Line>
                </Placeholder.Header>
                <Placeholder.Header image>
                  <Placeholder.Line></Placeholder.Line>
                  <Placeholder.Line></Placeholder.Line>
                </Placeholder.Header>
              </Placeholder>
            ) : (
              <Result
                status="404"
                title="No Comments !!!"
                subTitle="Wanna Put Some???"
              ></Result>
            )}
          </Comment.Group>
        </Modal.Content>
        <Divider style={{ marginBottom: 0, marginTop: 0 }}></Divider>
        <Modal.Content>
          {commentSectionLoading ? (
            <CircularProgress></CircularProgress>
          ) : (
            <input
              placeholder="Comment..."
              value={commentDescription}
              onChange={(e) => {
                setCommentDescription(e.target.value);
              }}
              style={{
                width: "80%",
                border: "none",
                fontSize: 14,
                fontFamily: "poppins",
                padding: 4,
                outline: "none",
              }}
            ></input>
          )}
          <button
            disabled={commentDescription.length > 0 ? false : true}
            onClick={async () => {
              try {
                const token = localStorage.getItem("AnonymityToken#123");
                setCommentSectionLoading(true);
                const res = await postComment(
                  props.id,
                  token,
                  commentDescription
                );
                const bdy = await res.json();
                if (res.status === 200) {
                  setCommentCount(commentCount + 1);
                  setComments(
                    comments.concat([
                      {
                        description: commentDescription,
                        By: {
                          profilephoto: props.profilePhoto1,
                          firstName: "By",
                          LastName: "Me... Right Now",
                        },
                      },
                    ])
                  );
                  setCommentDescription("");
                }
                setCommentSectionLoading(false);
              } catch (e) {
                setCommentSectionLoading(false);
              }
            }}
            style={{
              padding: 4,
              color: commentDescription.length == 0 ? blue[100] : blue[500],
              float: "right",
              background: "transparent",
              fontSize: 15,
              fontFamily: "poppins",
              border: "none",
              outline: "none",
              cursor: "pointer",
              marginRight: 20,
            }}
          >
            Post
          </button>
        </Modal.Content>
      </Modal>
    </Container>
  );
};
const mapStateToProps = (state) => {
  return {
    profilePhoto1: state.userReducer.profilePhoto,
    currentTab: state.navigationReducer.currentTab,
  };
};

export default connect(mapStateToProps)(Problem);

