import React from "react";
import { Grid, CircularProgress, Divider } from "@material-ui/core";
import { Pagination } from "@material-ui/lab";
import RenderProblems from "../renderProblems";
// import { Pagination } from "semantic-ui-react";
import { Result } from "antd";
import { CenterStuff } from "../../../Person/OthersProfile/profile/Profile";

const ForComp = (props) => {
  const { problemsCount } = props;

  return (
    <Grid container>
      <Grid item sm={3}></Grid>
      <Divider orientation="vertical" flexItem></Divider>
      <Grid item sm={6}>
        {props.Problems.length !== 0 && !props.isLoading ? (
          <div
            style={{ marginTop: 10, display: "flex", justifyContent: "center" }}
          >
            <RenderProblems {...props}></RenderProblems>
          </div>
        ) : !props.isError && !props.isLoading ? (
          <Result
            status="info"
            title="No Problems Here! Check Again Later..."
          ></Result>
        ) : props.isLoading ? (
          ""
        ) : (
          <Result
            status="error"
            title="There's Some Error Fetching Data From The Server"
          ></Result>
        )}

        {problemsCount > 10 && !props.isLoading ? (
          <CenterStuff style={{ marginTop: 100, marginBottom: 300 }}>
            <Pagination
              color="secondary"
              count={Math.floor(problemsCount / 10) + 1}
              page={props.pageNo}
              onChange={(e, page) => {
                props.setPageNo(page);
              }}
            ></Pagination>
          </CenterStuff>
        ) : (
          <div style={{ marginBottom: 100, marginTop: 100 }}></div>
        )}
        {props.isLoading ? (
          <div style={{ display: "flex", justifyContent: "center" }}>
            <CircularProgress
              style={{
                width: 100,
                height: 100,
                marginTop: 200,
                color: "black",
              }}
            ></CircularProgress>
          </div>
        ) : (
          ""
        )}
      </Grid>
      <Divider orientation="vertical" flexItem></Divider>
      <Grid item sm={3}></Grid>
    </Grid>
  );
};

export default ForComp;

