import React, { useState } from "react";
import { connect } from "react-redux";
import {
  Popover,
  IconButton,
  Avatar,
  Paper,
  List,
  ListItem,
  Divider,
} from "@material-ui/core";
import { AllOut, Telegram, Person } from "@material-ui/icons";
import { red, grey } from "@material-ui/core/colors";
import { logout, logoutall } from "../../ApiRequests/request";
const MyMenu = (props) => {
  const [anchor, setAnchor] = useState(null);

  return (
    <div>
      <IconButton
        onClick={(e) => {
          setAnchor(e.currentTarget);
        }}
      >
        <Avatar
          style={{ height: 30, width: 30 }}
          src={props.profilePhoto}
        ></Avatar>
      </IconButton>
      <Popover
        open={Boolean(anchor)}
        anchorEl={anchor}
        onClose={() => {
          setAnchor(null);
        }}
      >
        <Paper style={{ width: 300, background: grey[900] }} elevation={10}>
          <List>
            <ListItem>
              <Avatar src={props.profilePhoto} variant="rounded"></Avatar>
              <div
                style={{
                  marginLeft: 10,
                  fontFamily: "poppins",
                  fontWeight: "bold",
                  fontSize: 15,
                  color: "lightgrey",
                }}
              >
                {props.username}
              </div>
            </ListItem>
            <Divider style={{ marginTop: 10, marginBottom: 10 }}></Divider>
            <ListItem
              button
              onClick={() => {
                props.dispatch({ type: "Person" });
                props.history.push("/anonymity/person");
                setAnchor(null);
              }}
            >
              <Person
                style={{ color: "white", width: 30, height: 30 }}
              ></Person>
              <div
                style={{
                  marginLeft: 10,
                  fontFamily: "poppins",
                  fontWeight: 500,
                  fontSize: 10,
                  color: "white",
                }}
              >
                My Profile
              </div>
            </ListItem>
            <Divider></Divider>
            <ListItem
              button
              onClick={() => {
                logout().then((res) => {
                  if (res.ok) {
                    props.history.push("/login");
                    props.dispatch({ type: "notAuth" });
                  }
                });
              }}
            >
              <Telegram
                style={{ height: 30, width: 30, color: "grey" }}
              ></Telegram>
              <div
                style={{
                  marginLeft: 10,
                  fontFamily: "poppins",
                  fontWeight: 500,
                  fontSize: 10,
                  color: "lightgrey",
                }}
              >
                Logout (From This Device)
              </div>
            </ListItem>
            <ListItem
              button
              onClick={() => {
                logoutall().then((res) => {
                  if (res.ok) {
                    props.history.push("/login");
                    props.dispatch({ type: "notAuth" });
                  }
                });
              }}
            >
              <AllOut style={{ height: 30, width: 30, color: "grey" }}></AllOut>
              <div
                style={{
                  marginLeft: 10,
                  fontFamily: "poppins",
                  fontWeight: 500,
                  fontSize: 10,
                  color: "lightgrey",
                }}
              >
                Logout (From All Devices)
              </div>
            </ListItem>
          </List>
        </Paper>
      </Popover>
    </div>
  );
};

export default connect()(MyMenu);
