import React from "react";
import { connect } from "react-redux";
import ProfileButton from "../profileButtonMenu";
import {
  AppBar,
  createMuiTheme,
  ThemeProvider,
  Typography,
  Toolbar,
  Button,
  Slide,
  IconButton,
  Icon,
} from "@material-ui/core";
import { Divider } from "semantic-ui-react";
import Search1 from "../../Search/Search";
import "../styles/lift.css";
import {
  setAbout,
  setRanking,
  setHome,
  setNearBy,
  setPerson,
} from "../../../redux/action";
import { Image } from "semantic-ui-react";
import { orange } from "@material-ui/core/colors";
import {
  Ranking,
  NearBy,
  Home as home,
  About,
} from "../../../redux/actionTypes";
import "./style.css";
import { Person, VerticalAlignTopOutlined } from "@material-ui/icons";
import { BackTop } from "antd";
const myOrange = "rgba(255,165,0,1)";

const theme1 = createMuiTheme({
  palette: {
    secondary: {
      main: "rgba(255,255,255,1)",
    },
    primary: orange,
  },
});

const ForComputer = (props) => {
  return (
    <ThemeProvider theme={theme1}>
      <BackTop style={{ position: "fixed", left: "4%", bottom: "10%" }}>
        <IconButton
          style={{
            background: "rgba(0,0,0,0.5)",
            color: "white",
            boxShadow: "1px 1px 15px grey",
          }}
        >
          <VerticalAlignTopOutlined></VerticalAlignTopOutlined>
        </IconButton>
      </BackTop>
      <AppBar color="secondary" elevation={0} position="static">
        <Toolbar
          style={{
            marginLeft: "8%",
            marginRight: "1%",
          }}
        >
          <Image
            src={require("../../../images/complainify.png")}
            size="tiny"
            style={{ marginRight: 5 }}
          ></Image>
          <Typography
            style={{
              color: "rgba(0,0,0,0.6)",
              float: "left",
              fontWeight: "bold",
              marginRight: "5%",
              // flexGrow: 1
            }}
            variant="h6"
          >
            Anonymity
          </Typography>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <Button
              size="large"
              onClick={() => {
                props.setHome();
                props.history.push("/anonymity/" + home);
              }}
              style={{
                marginRight: 10,
                textTransform: "none",
                transition: "all 0.15s",
                fontFamily: "poppins",

                borderBottom:
                  props.currentTab === "Home"
                    ? "2px solid black"
                    : "2px solid white",
                borderRadius: "0px",
                transform:
                  props.currentTab === "Home"
                    ? "translateY(-5px)"
                    : "translateY(0px)",
                fontWeight: props.currentTab === "Home" ? "bold" : "",
              }}
            >
              <Image
                size="mini"
                style={{ marginRight: 1 }}
                src="https://img.icons8.com/color/144/000000/home.png"
              />
              Home
            </Button>
            <Button
              disableElevation
              onClick={() => {
                props.setNearBy();
                props.history.push("/anonymity/" + NearBy);
              }}
              size="large"
              style={{
                marginRight: 10,
                textTransform: "none",
                transition: "all 0.15s",

                fontFamily: "poppins",
                borderBottom:
                  props.currentTab === "Nearby"
                    ? "2px solid black"
                    : "2px solid white",
                borderRadius: "0px",
                transform:
                  props.currentTab === "Nearby"
                    ? "translateY(-5px)"
                    : "translateY(0px)",
                fontWeight: props.currentTab === "Nearby" ? "bold" : "",
              }}
            >
              <Image
                size="mini"
                style={{ marginRight: 1 }}
                src="https://img.icons8.com/color/96/000000/gps-device.png"
              />
              Nearby
            </Button>
            <Button
              disableElevation
              onClick={() => {
                props.setPerson();
                props.history.push("/anonymity/" + "person");
              }}
              size="large"
              style={{
                marginRight: 10,
                textTransform: "none",
                transition: "all 0.15s",

                fontFamily: "poppins",
                borderBottom:
                  props.currentTab === "Person"
                    ? "2px solid black"
                    : "2px solid white",
                borderRadius: "0px",
                transform:
                  props.currentTab === "Person"
                    ? "translateY(-5px)"
                    : "translateY(0px)",
                fontWeight: props.currentTab === "Person" ? "bold" : "",
              }}
            >
              <Image
                size="mini"
                style={{ marginRight: 1 }}
                src="https://img.icons8.com/color/48/000000/person-male.png"
              />
              Person
            </Button>
            <Button
              size="large"
              style={{
                marginRight: 10,
                textTransform: "none",
                transition: "all 0.15s",

                fontFamily: "poppins",
                borderBottom:
                  props.currentTab === "Ranking"
                    ? "2px solid black"
                    : "2px solid white",
                borderRadius: "0px",
                transform:
                  props.currentTab === "Ranking"
                    ? "translateY(-5px)"
                    : "translateY(0px)",
                fontWeight: props.currentTab === "Ranking" ? "bold" : "",
              }}
              onClick={() => {
                props.setRanking();
                props.history.push("/anonymity/" + Ranking);
              }}
            >
              <Image
                size="mini"
                style={{ marginRight: 1 }}
                src="https://img.icons8.com/color/48/000000/ratings.png"
              />
              Ranking
            </Button>
            <Button
              size="large"
              onClick={() => {
                props.setAbout();
                props.history.push("/anonymity/" + About);
              }}
              style={{
                marginRight: 10,
                transition: "all 0.15s",

                textTransform: "none",
                fontFamily: "poppins",
                transform:
                  props.currentTab === "About"
                    ? "translateY(-5px)"
                    : "translateY(0px)",
                borderBottom:
                  props.currentTab === "About"
                    ? "2px solid black"
                    : "2px solid white",
                borderRadius: "0px",
                fontWeight: props.currentTab === "About" ? "bold" : "",
              }}
            >
              <Image
                size="mini"
                style={{ marginRight: 1 }}
                src="https://img.icons8.com/color/48/000000/about.png"
              />
              About
            </Button>
          </div>
          <div style={{ flexGrow: 1 }}></div>
          <Search1 history={props.history}></Search1>
          <ProfileButton {...props}></ProfileButton>
        </Toolbar>
      </AppBar>
      <Divider style={{ marginTop: 0, marginBottom: 0 }}></Divider>
    </ThemeProvider>
  );
};
// <Person style={{ marginRight: 10, color: "orange" }}></Person>

// const mapStateToProps = state => {
//   const { navigationReducer } = state;
//   return {
//     currentTab: navigationReducer.currentTab
//   };
// };

const mapStateToProps = (state) => {
  return {
    currentTab: state.navigationReducer.currentTab,
    width: state.visibility.Width,
    username: state.userReducer.username,
  };
};

export default connect(mapStateToProps, {
  setAbout,
  setRanking,
  setHome,
  setNearBy,
  setPerson,
})(ForComputer);

