import React from "react";
import { connect } from "react-redux";
import { Responsive } from "semantic-ui-react";
import { ProfileInfo, CenterStuff } from "../OthersProfile/profile/Profile";
import { CircularProgress, Typography } from "@material-ui/core";
import url from "../../../strings/url";
import { Edit } from "../../Edit/Edit";
class MyProfile extends React.PureComponent {
  state = {
    userInfo: {},
    loading: true,
    edit: false,
    editType: null,
  };

  childProps = {
    username: "paraskanwar",
  };
  componentWillMount() {
    const headers = new Headers();
    headers.append("Authorization", localStorage.getItem("AnonymityToken#123"));
    fetch(`${url}/getMyProfile`, { method: "GET", headers }).then(
      async (res) => {
        this.setState({ loading: false, userInfo: await res.json() });
      }
    );
  }
  render() {
    const { editType, loading, userInfo, edit } = this.state;
    return (
      <div>
        {edit ? (
          <Edit
            editField={editType}
            open={edit}
            onClose={() => {
              this.setState({ edit: false });
            }}
          ></Edit>
        ) : (
          ""
        )}
        <CenterStuff style={{ marginTop: 10 }}>
          <Typography
            style={{ fontSize: 30, fontFamily: "poppins", fontWeight: "bold" }}
          >
            My Profile
          </Typography>
        </CenterStuff>
        {loading ? (
          <CenterStuff style={{ marginTop: "20%" }}>
            <CircularProgress style={{ color: "black" }}></CircularProgress>
          </CenterStuff>
        ) : (
          <div>
            <Responsive {...Responsive.onlyComputer}>
              <CenterStuff style={{ marginTop: 10, marginBottom: 10 }}>
                <ProfileInfo
                  setType={(type) => {
                    this.setState({ editType: type });
                  }}
                  {...userInfo}
                  openEdit={() => {
                    this.setState({ edit: true });
                  }}
                  editable={true}
                  deviceType="com"
                ></ProfileInfo>
              </CenterStuff>
            </Responsive>
            <Responsive {...Responsive.onlyTablet}>
              <CenterStuff style={{ marginTop: 10, marginBottom: 10 }}>
                <ProfileInfo
                  setType={(type) => {
                    this.setState({ editType: type });
                  }}
                  {...userInfo}
                  openEdit={() => {
                    this.setState({ edit: true });
                  }}
                  editable={true}
                  deviceType="tab"
                ></ProfileInfo>
              </CenterStuff>
            </Responsive>
            <Responsive {...Responsive.onlyMobile}>
              <CenterStuff style={{ marginTop: "5%", marginBottom: "20%" }}>
                <ProfileInfo
                  setType={(type) => {
                    this.setState({ editType: type });
                  }}
                  {...userInfo}
                  openEdit={() => {
                    this.setState({ edit: true });
                  }}
                  editable={true}
                  deviceType="mob"
                ></ProfileInfo>
              </CenterStuff>
            </Responsive>
          </div>
        )}
      </div>
    );
  }
}

export default connect()(MyProfile);
