const initialState = {
  username: "Unknown",
  email: "unknown",
  college: "unknown",
  profilePhoto:
    "https://cdn0.iconfinder.com/data/icons/flat-design-business-set-3/24/people-customer-unknown-512.png",
  backgroundPhoto: "none",
  token: null,
};

export const userReducer = (state = initialState, action) => {
  console.log(action.user);
  switch (action.type) {
    case "setUser":
      return {
        ...state,
        ...action.user,
      };
    default:
      return state;
  }
};
