import React, { useState } from "react";
import { Grid, CircularProgress, Divider } from "@material-ui/core";
import RenderProblems from "../renderProblems";
import { Pagination } from "semantic-ui-react";
import { Result } from "antd";

const ForComp = (props) => {
  const { problemsCount } = props;

  return (
    <Grid container>
      <Grid item sm={5}></Grid>
      <Divider orientation="vertical" flexItem></Divider>
      <Grid item sm={6}>
        {props.Problems.length !== 0 && !props.isLoading ? (
          <RenderProblems {...props}></RenderProblems>
        ) : !props.isError && !props.isLoading ? (
          <Result
            status="info"
            title="No Problems Here! Check Again Later..."
          ></Result>
        ) : props.isLoading ? (
          ""
        ) : (
          <Result
            status="error"
            title="There's Some Error Fetching Data From The Server"
          ></Result>
        )}

        {problemsCount > 10 && !props.isLoading ? (
          <div style={{ display: "flex", justifyContent: "center" }}>
            <Pagination
              pointing
              secondary
              siblingRange={0}
              onPageChange={(event, { activePage }) => {
                props.setPageNo(activePage);
              }}
              activePage={props.pageNo}
              totalPages={Math.floor(problemsCount / 10) + 1}
              boundaryRange={1}
            ></Pagination>
          </div>
        ) : (
          ""
        )}
        {props.isLoading ? (
          <div style={{ display: "flex", justifyContent: "center" }}>
            {" "}
            <CircularProgress
              style={{
                width: 100,
                height: 100,
                marginTop: 200,
                color: "black",
              }}
            ></CircularProgress>
          </div>
        ) : (
          ""
        )}
      </Grid>
    </Grid>
  );
};

export default ForComp;
