export const getSensitiveContent = (
  skip,
  options = { sortBy: -1, filter: null }
) => {
  const headers = new Headers();
  headers.append("Authorization", localStorage.getItem("AnonymityToken#123"));
  headers.append("content-type", "application/json");
  headers.append("sortBy", true);
  const body = JSON.stringify(options);
  return fetch(`${url}/user/post/sensitive/${skip}`, {
    method: "POST",
    headers,
    body,
  });
};
