import React, { useState } from "react";
import Search1 from "../../Search/Search";
import { connect } from "react-redux";
import {
  setAbout,
  setRanking,
  setHome,
  setNearBy,
  setPerson,
} from "../../../redux/action";
import {
  createMuiTheme,
  ThemeProvider,
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Button,
  SwipeableDrawer,
  List,
  ListItem,
  Fab,
  Zoom,
  Tooltip,
} from "@material-ui/core";
import {
  MenuOutlined,
  Home,
  NearMe,
  Info,
  ListAltOutlined as List1,
  Person,
} from "@material-ui/icons";
import { orange as orange1 } from "@material-ui/core/colors";
import { Divider, Image, Search, Visibility } from "semantic-ui-react";
import ProfileButtonMenu from "../profileButtonMenu";

const theme1 = createMuiTheme({
  palette: {
    secondary: {
      main: "rgba(255,255,255,1)",
    },
    primary: orange1,
  },
});

const marginMy = "30%";

const orange = "rgba(255,165,0,1)";
const white = "rgba(255,255,255,1)";
const grey = "rgba(0,0,0,0.5)";

const ForTablet = (props) => {
  const [showDrawer, setDrawer] = useState(false);
  const [showFab, setFab] = useState(false);

  return (
    <ThemeProvider theme={theme1}>
      <Visibility
        onUpdate={(e, { calculations }) => {
          setFab(!calculations.topVisible);
        }}
      >
        <Tooltip title="Menu">
          <Zoom in={showFab}>
            <Fab
              style={{
                zIndex: 100,
                position: "fixed",
                right: "2%",
                color: "white",
                bottom: "2%",
              }}
              color="primary"
              onClick={() => {
                setDrawer(true);
              }}
            >
              <MenuOutlined></MenuOutlined>
            </Fab>
          </Zoom>
        </Tooltip>
        <AppBar color="secondary" elevation={0} position="sticky">
          <Toolbar>
            <IconButton onClick={() => setDrawer(true)}>
              <MenuOutlined></MenuOutlined>
            </IconButton>
            <Button
              onClick={() => {
                props.history.push("/anonymity/" + "Home");
                props.setHome();
              }}
              style={{ marginLeft: "1%" }}
            >
              <Image
                src={require("../../../images/complainify.png")}
                size="tiny"
              ></Image>
              <Typography
                variant="subtitle1"
                style={{
                  fontWeight: "bold",
                  color: "grey",
                  fontFamily: "poppins",
                }}
              >
                Anonymity
              </Typography>
            </Button>
            <div style={{ flexGrow: 1 }}></div>
            <Search1 history={props.history}></Search1>
            <ProfileButtonMenu {...props}></ProfileButtonMenu>
          </Toolbar>
        </AppBar>
        <SwipeableDrawer
          open={showDrawer}
          onClose={() => setDrawer(false)}
          onOpen={() => setDrawer(true)}
        >
          <List>
            <div style={{ display: "flex", justifyContent: "center" }}>
              <Image
                src={require("../../../images/complainify.png")}
                size="tiny"
              ></Image>
            </div>
            <div style={{ display: "flex", justifyContent: "center" }}></div>

            <Divider></Divider>
            <ListItem
              style={{
                fontSize: 20,
                width: 300,
                color: "black",
                textAlign: "center",
                background: props.currentTab == "Home" ? orange : "",
                marginBottom: 20,
              }}
              onClick={() => {
                props.history.push("/anonymity/Home");
                setDrawer(false);
                props.setHome();
              }}
              button
            >
              <Home
                style={{
                  color: props.currentTab == "Home" ? white : grey,
                  marginRight: marginMy,
                }}
              ></Home>
              <div style={{ textAlign: "center" }}>Home</div>
            </ListItem>
            <ListItem
              style={{
                fontSize: 20,
                width: 300,
                color: "black",
                textAlign: "center",
                background: props.currentTab == "Person" ? orange : "",
                marginBottom: 20,
              }}
              onClick={() => {
                props.history.push("/anonymity/Person");
                setDrawer(false);
                props.setPerson();
              }}
              button
            >
              <Person
                style={{
                  color: props.currentTab == "Person" ? white : grey,
                  marginRight: marginMy,
                }}
              ></Person>
              <div style={{ textAlign: "center" }}>Person</div>
            </ListItem>
            <ListItem
              button
              onClick={() => {
                props.history.push("/anonymity/Nearby");
                setDrawer(false);
                props.setNearBy();
              }}
              style={{
                fontSize: 20,
                marginBottom: 20,
                background: props.currentTab == "Nearby" ? orange : "",
              }}
            >
              <NearMe
                style={{
                  marginRight: marginMy,
                  color: props.currentTab === "Nearby" ? white : grey,
                }}
              ></NearMe>
              NearBy
            </ListItem>
            <ListItem
              style={{
                fontSize: 20,
                color: "black",
                marginBottom: 20,
                textAlign: "center",
                background: props.currentTab == "Ranking" ? orange : "",
              }}
              onClick={() => {
                props.history.push("/anonymity/Ranking");
                setDrawer(false);
                props.setRanking();
              }}
              button
            >
              <List1
                style={{
                  color: props.currentTab == "Ranking" ? white : grey,
                  marginRight: marginMy,
                }}
              ></List1>
              Ranking
            </ListItem>
            <Divider></Divider>
            <ListItem
              style={{
                color: "black",
                textAlign: "center",
                marginBottom: 20,
                background: props.currentTab == "About" ? orange : "",
                fontSize: 20,
              }}
              onClick={() => {
                props.history.push("/anonymity/" + "About");
                setDrawer(false);
                props.setAbout();
              }}
              button
            >
              <Info
                style={{
                  color: props.currentTab == "About" ? white : grey,
                  marginRight: marginMy,
                }}
              ></Info>
              About
            </ListItem>
          </List>
        </SwipeableDrawer>
        <Divider style={{ marginTop: 0 }}></Divider>
      </Visibility>
    </ThemeProvider>
  );
};
const mapStateToProps = (state) => {
  return {
    currentTab: state.navigationReducer.currentTab,
  };
};

export default connect(mapStateToProps, {
  setAbout,
  setRanking,
  setHome,
  setNearBy,
  setPerson,
})(ForTablet);
