import React, { useState } from "react";
import Header from "../NotAuthHeader/Header";
import {
  Button,
  Container,
  Backdrop,
  CircularProgress
} from "@material-ui/core";
import { red } from "@material-ui/core/colors";
import { Divider } from "semantic-ui-react";
import url from "../../strings/url";
import { connect } from "react-redux";
const Al = props => {
  const [showBackDrop, handleBackdrop] = useState(false);
  const handleLogOut = () => {
    const token = localStorage.getItem("AnonymityToken#123");
    props.dispatch({ type: "notAuth" });
    localStorage.setItem("AnonymityToken#123", false);
    if (token) {
      const headers = new Headers();
      headers.append("content-type", "application/json");
      headers.append("Authorization", token);
      const request = new Request(`${url}/user/logout`, {
        method: "GET",
        headers
      });
      handleBackdrop(true);
      fetch(request)
        .then(res => {
          console.log(res.status);
          if (res.status === 200) {
            props.makeFalse();
          } else if (res.status === 401) {
            handleBackdrop(false);
          }
        })
        .catch(e => {
          handleBackdrop(false);
        });
    }
  };
  return (
    <div>
      <Header
        heading={`Seems Like You Are Already Logged In...`}
        username={props.username}
        h2={true}
      ></Header>
      <Backdrop in={showBackDrop} open={showBackDrop} style={{ zIndex: 1000 }}>
        <CircularProgress
          style={{ color: "white", width: 100, height: 100 }}
        ></CircularProgress>
      </Backdrop>
      <Container>
        <div
          style={{ display: "flex", justifyContent: "center", marginTop: 100 }}
        >
          <Button
            style={{
              color: "black",

              fontFamily: "poppins",
              fontSize: 15
            }}
            onClick={() => props.history.push("/anonymity/home")}
          >
            Go Home
          </Button>
        </div>
        <Divider horizontal>Or</Divider>
        <div style={{ display: "flex", justifyContent: "center" }}>
          <Button
            style={{
              color: red[500],

              fontFamily: "poppins",
              fontSize: 15
            }}
            onClick={handleLogOut}
          >
            Log Out
          </Button>
        </div>
      </Container>
    </div>
  );
};

export default connect()(Al);
