import { Home, About, Ranking, NearBy, Person } from "../actionTypes";

const initialState = {
  currentTab: "Home",
};
const navigationReducer = (state = initialState, action) => {
  switch (action.type) {
    case Home:
      return {
        ...state,
        currentTab: Home,
      };
    case About:
      return {
        currentTab: About,
      };
    case Ranking:
      return {
        currentTab: Ranking,
      };
    case NearBy:
      return {
        currentTab: NearBy,
      };
    case Person:
      return {
        currentTab: Person,
      };
    default:
      return state;
  }
};

export default navigationReducer;
