import React from "react";
import { Grid } from "@material-ui/core";
import { Responsive, Divider } from "semantic-ui-react";
const ForMob = (props) => {
  return (
    <Grid Container>
      <Grid item xs={10} style={{ display: "flex", justifyContent: "center" }}>
        {props.children}
      </Grid>
    </Grid>
  );
};

const ForTC = (props) => {
  return (
    <Grid container>
      <Grid item xs={3}></Grid>
      <Grid item xs={6} style={{ display: "flex", justifyContent: "center" }}>
        {props.children}
      </Grid>
      <Grid item xs={3}></Grid>
    </Grid>
  );
};

const Description = (props) => {
  return (
    <div>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          fontFamily: "poppins",
          fontWeight: 500,
          fontSize: 20,
          padding: 20,
          marginBottom: 100,
          flexGrow: 1,
        }}
      >
        This Website Basically works as a Complaint Management website for the
        College. you can post your complaints (either Anonymously or vice versa)
        and If your Post Manage To get Enough UpVotes (Remember DownVotes Will
        Also get Counted "-ve") Then That Problem Will Be Visible To Your Staff
        And They'll Take Action if they find that your complaint is Worth
        Considering. This Website Also Allow Staff To Post Notices. And Also
        There is a Separated Section for Notes Where Teachers Can Post Notes in
        an Organized Manner On The Website So That Any Student Can Access Them.
      </div>
      <Divider
        horizontal
        style={{
          fontSize: 13,
          fontFamily: "poppins",
          marginBottom: 100,
          textTransform: "none",
        }}
      >
        By @Paraskanwar
      </Divider>
    </div>
  );
};

export const About = (props) => {
  return (
    <div>
      <Responsive {...Responsive.onlyComputer}>
        <ForTC>
          <Description></Description>
        </ForTC>
      </Responsive>
      <Responsive {...Responsive.onlyTablet}>
        <ForTC>
          <Description></Description>
        </ForTC>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        <Description></Description>
      </Responsive>
    </div>
  );
};
