import React, { useState, useEffect } from "react";
import { Responsive, Visibility } from "semantic-ui-react";
import ForComp from "./forComp/forComp";
import ForTab from "./forTablet/forTab";
import ForMob from "./forMobile/forMob";
import { getNearByProblems } from "../../../ApiRequests/request";

import { connect } from "react-redux";
import { SpeedDial, SpeedDialAction, SpeedDialIcon } from "@material-ui/lab";
import {
  ThemeProvider,
  Menu,
  MenuItem,
  createMuiTheme,
  Backdrop,
  Checkbox,
} from "@material-ui/core";
import { orange } from "@material-ui/core/colors";
import { Filter, Sort, ListAlt } from "@material-ui/icons";
const theme = createMuiTheme({
  palette: {
    primary: orange,
    secondary: {
      main: "rgba(255,255,255,0.5)",
    },
  },
});

const filterMenu = [
  { type: "none", name: "Show All" },
  // { type: "upperAuth", name: "Visible To Faculty ?" },
  // { type: "nonUpperAuth", name: "Not Visible To Faculty" },
  { type: "public", name: "Public" },
  { type: "anonymous", name: "Anonymous" },
];
const sortMenu = [
  { type: "latest", name: "Latest" },
  { type: "oldest", name: "Oldest" },
  // { type: "upVotes", name: "By UpVotes" },
  // { type: "downVotes", name: "By DownVotes" },
];

const Problems = (props) => {
  const [isError, setError] = useState(false);
  const [isLoading, setLoading] = useState(false);
  const [pageNo, setPageNo] = useState(1);
  const [Problems, setProblems] = useState([]);
  const [requestMade, setRequestMade] = useState(0);
  const [totalProblems, setTotal] = useState(0);
  const [whichState, setState] = useState(null);
  const getProblems = async (pageNo, options) => {
    try {
      setLoading(true);
      const response = await getNearByProblems(
        props.token,
        (pageNo - 1) * 10,
        options
      );
      if (response.ok) {
        const { problems, count } = await response.json();
        if (problems) {
          setProblems(problems);
          setTotal(count);
          setLoading(false);
        }
        return;
      }
      setLoading(false);
      setError(true);
    } catch (e) {
      setLoading(false);
      setError(true);

      console.log(e.message);
    }
  };
  const [elAnchor, setElAnchor] = useState(null);
  const [isSpeedDialOpen, setSpeedDial] = useState(false);
  const [scrollDirection, setScrollDirection] = useState("up");
  const [filter, setFilter] = useState({ current: "none", prev: "none" });
  const [sortBy, setSortBy] = useState({ current: "latest", prev: "latest" });

  useEffect(() => {
    if (requestMade === 0) {
      getProblems(pageNo, { sortBy: sortBy.current, filter: filter.current });
      setRequestMade(1);
    }

    if (filter.current !== filter.prev || sortBy.current !== sortBy.prev) {
      getProblems(1, { sortBy: sortBy.current, filter: filter.current });
      setPageNo(1);
      setFilter({ ...filter, prev: filter.current });
      setSortBy({ ...sortBy, prev: sortBy.current });
    }
  });
  return (
    <div>
      <ThemeProvider theme={theme}>
        <Visibility
          onUpdate={(e, visibility) => {
            setScrollDirection(visibility.calculations.direction);
          }}
        >
          <Menu
            anchorEl={elAnchor}
            open={Boolean(elAnchor)}
            onClose={() => {
              setElAnchor(null);
              setState(null);
            }}
          >
            {whichState === "filter"
              ? filterMenu.map((item) => {
                  return (
                    <MenuItem
                      key={item.name}
                      style={{ width: 200 }}
                      onClick={() => {
                        setFilter({ ...filter, current: item.type });
                        setElAnchor(null);
                        setSpeedDial(false);
                      }}
                    >
                      <Checkbox
                        checked={filter.current === item.type}
                        color="primary"
                      ></Checkbox>
                      <div
                        style={{
                          fontFamily: "poppins",
                          fontWeight: "bold",
                          fontSize: 13,
                        }}
                      >
                        {item.name}
                      </div>
                    </MenuItem>
                  );
                })
              : sortMenu.map((item) => {
                  return (
                    <MenuItem
                      style={{ width: 200 }}
                      key={item.name}
                      onClick={() => {
                        setSortBy({ ...sortBy, current: item.type });
                        setElAnchor(null);
                        setSpeedDial(false);
                      }}
                    >
                      <Checkbox
                        color="primary"
                        name={item.type}
                        checked={sortBy.current === item.type}
                      ></Checkbox>
                      <div
                        style={{
                          fontFamily: "poppins",
                          fontWeight: "bold",
                          fontSize: 13,
                        }}
                      >
                        {item.name}
                      </div>
                    </MenuItem>
                  );
                })}
          </Menu>

          <div
            style={{
              position: "fixed",
              right: "2%",
              bottom: "10%",
              zIndex: 100,
            }}
          >
            <SpeedDial
              ariaLabel="filter and sorter"
              icon={<ListAlt style={{ color: "white" }}></ListAlt>}
              open={isSpeedDialOpen}
              color="secondary"
              direction={"up"}
              hidden={scrollDirection !== "up" && !isSpeedDialOpen}
              onClose={() => {
                setSpeedDial(false);
              }}
              onOpen={() => {
                setSpeedDial(true);
              }}
            >
              <SpeedDialAction
                icon={<Filter></Filter>}
                tooltipTitle="Filters"
                tooltipOpen
                onClick={(e) => {
                  setState("filter");
                  setElAnchor(e.currentTarget);
                }}
              ></SpeedDialAction>
              <SpeedDialAction
                onClick={(e) => {
                  setState("sort");
                  setElAnchor(e.currentTarget);
                }}
                tooltipOpen
                tooltipTitle="SortBy"
                icon={<Sort></Sort>}
              ></SpeedDialAction>
            </SpeedDial>
          </div>
          <Responsive {...Responsive.onlyComputer}>
            <ForComp
              deviceType={"comp"}
              Problems={Problems}
              id={props.id}
              pageNo={pageNo}
              setPageNo={(pageNo) => {
                getProblems(pageNo, {
                  sortBy: sortBy.current,
                  filter: filter.current,
                });
                setPageNo(pageNo);
              }}
              problemsCount={totalProblems}
              isError={isError}
              isLoading={isLoading}
              {...props}
            ></ForComp>
          </Responsive>
          <Responsive {...Responsive.onlyTablet}>
            <ForTab
              {...props}
              deviceType={"tab"}
              Problems={Problems}
              id={props.id}
              pageNo={pageNo}
              setPageNo={(pageNo) => {
                getProblems(pageNo, {
                  sortBy: sortBy.current,
                  filter: filter.current,
                });
                setPageNo(pageNo);
              }}
              problemsCount={totalProblems}
              isError={isError}
              isLoading={isLoading}
            ></ForTab>
          </Responsive>
          <Responsive {...Responsive.onlyMobile}>
            <ForMob
              {...props}
              deviceType={"mob"}
              Problems={Problems}
              id={props.id}
              pageNo={pageNo}
              setPageNo={(pageNo) => {
                getProblems(pageNo, {
                  sortBy: sortBy.current,
                  filter: filter.current,
                });
                setPageNo(pageNo);
              }}
              problemsCount={totalProblems}
              isError={isError}
              isLoading={isLoading}
            ></ForMob>
          </Responsive>
        </Visibility>
      </ThemeProvider>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.userReducer.token,
    id: state.userReducer.id,
  };
};

export default connect(mapStateToProps)(Problems);
