import React, { useState } from "react";
import { connect } from "react-redux";
import {
  Paper,
  List,
  Divider,
  ListItem,
  createMuiTheme,
  ThemeProvider,
  ListSubheader,
  Avatar,
  TextField,
  CircularProgress,
  LinearProgress,
} from "@material-ui/core";
import "./Search.css";
import { blueGrey, orange, grey } from "@material-ui/core/colors";
import { Skeleton } from "@material-ui/lab";
import { findPeople } from "../../ApiRequests/request";
const theme = createMuiTheme({
  palette: {
    primary: blueGrey,
    secondary: orange,
  },
});
const Search = (props) => {
  const [toFind, setFind] = useState("");
  const [searchResult, setSearchResult] = useState([]);
  const [Loading, setLoading] = useState(false);

  const handleSearchChange = (e) => {
    setLoading(true);
    findPeople(e.target.value)
      .then(async (res) => {
        const result = await res.json();

        setSearchResult(result);
        setLoading(false);
      })
      .catch((e) => {
        setSearchResult([]);
        setLoading(false);
      });

    setFind(e.target.value);
  };

  return (
    <ThemeProvider theme={theme}>
      <Paper
        color="primary"
        style={{
          // backgroundColor: blueGrey[900],
          backgroundColor: grey[900],
          width: 300,

          fontFamily: "poppins",
          color: "lightgrey",
        }}
        elevation={10}
      >
        <List>
          <div style={{ justifyContent: "center", display: "flex" }}>
            <input
              value={toFind}
              onChange={handleSearchChange}
              style={{ marginTop: 10 }}
              className="mySearch"
              placeholder="Search People"
            ></input>
          </div>
          <Divider style={{ marginTop: 10, marginBottom: 10 }}></Divider>
          {Loading ? (
            <ListItem>
              <LinearProgress
                style={{ width: "100%" }}
                color="primary"
              ></LinearProgress>
            </ListItem>
          ) : (
            ""
          )}
          {searchResult.length > 0 && !Loading ? (
            searchResult.map((result) => {
              return (
                <ListItem
                  key={result.LastName}
                  button
                  style={{ fontWeight: "lighter" }}
                  onClick={() => {
                    props.history.push(`/anonymity/person/${result._id}`);

                    if (props.currentTab === "Person") {
                      window.location.reload(false);
                    }
                    props.dispatch({ type: "Person" });

                    props.setAnchor(null);
                  }}
                >
                  <Avatar
                    style={{ marginRight: 10 }}
                    src={result.profilephoto}
                  ></Avatar>
                  {result.firstName + " " + result.LastName}
                </ListItem>
              );
            })
          ) : !Loading && toFind.length > 0 ? (
            <ListSubheader
              style={{ color: "grey", fontSize: 16, fontFamily: "poppins" }}
            >
              No Results
            </ListSubheader>
          ) : (
            ""
          )}
        </List>
      </Paper>
    </ThemeProvider>
  );
};
const mstp = (state) => {
  return {
    currentTab: state.navigationReducer.currentTab,
  };
};
export default connect(mstp)(Search);
