import React, { useState } from "react";
import { connect } from "react-redux";

import {
  createMuiTheme,
  ThemeProvider,
  CircularProgress,
} from "@material-ui/core";
import { Result } from "antd";
import Profile from "./profile/Profile";
import { Divider } from "semantic-ui-react";
import { PeopleProfile } from "../../../ApiRequests/request";
const theme = createMuiTheme({
  palette: {
    primary: {
      main: "rgba(0,0,0,1)",
    },
    secondary: {
      main: "rgba(0,0,0,1)",
    },
  },
});
const Others = (props) => {
  const [info, setInfo] = useState(null);
  const [isLoading, setLoading] = useState(true);

  if (!Boolean(info)) {
    console.log("i am inside this");
    PeopleProfile(props.match.params.id)
      .then(async (res) => {
        if (res.ok) {
          setInfo(await res.json());
          setLoading(false);
        }
        setLoading(false);
      })
      .catch((e) => {
        setLoading(false);
      });
  }
  return (
    <div>
      <div
        style={{
          justifyContent: "center",
          display: "flex",
          marginTop: 10,
        }}
      >
        <ThemeProvider theme={theme}>
          {isLoading ? (
            <CircularProgress></CircularProgress>
          ) : info ? (
            <Profile
              history={props.history}
              {...info}
              userId={props.match.params.id}
            ></Profile>
          ) : (
            <Result status="500" title="Internal Server Error"></Result>
          )}
        </ThemeProvider>
      </div>
      <Divider style={{ margin: 100 }} hidden></Divider>
    </div>
  );
};

export default connect()(Others);
