import React from "react";
import { connect } from "react-redux";

import {
  setAbout,
  setRanking,
  setHome,
  setNearBy,
  setPerson,
} from "../../redux/action";

import HeaderComp from "./forComputers/computer";
import HeaderTab from "./forTablet/tablet";
import { Responsive } from "semantic-ui-react";
import HeaderMob from "./forMobiles/mobile";
class Home extends React.Component {
  constructor(props) {
    super(props);
  }
  componentDidMount() {
    if (this.props.location.pathname.toLowerCase().includes("home"))
      this.props.setHome();
    if (this.props.location.pathname.toLowerCase().includes("ranking"))
      this.props.setRanking();
    if (this.props.location.pathname.toLowerCase().includes("about"))
      this.props.setAbout();
    if (this.props.location.pathname.toLowerCase().includes("nearby"))
      this.props.setNearBy();
    if (this.props.location.pathname.toLowerCase().includes("person"))
      this.props.setPerson();
  }
  render() {
    return (
      <div>
        <Responsive {...Responsive.onlyComputer}>
          <HeaderComp
            username={this.props.username}
            profilePhoto={this.props.profilePhoto}
            history={this.props.history}
          ></HeaderComp>
        </Responsive>

        <Responsive {...Responsive.onlyTablet}>
          <HeaderTab
            username={this.props.username}
            profilePhoto={this.props.profilePhoto}
            history={this.props.history}
          ></HeaderTab>
        </Responsive>
        <Responsive {...Responsive.onlyMobile}>
          <HeaderMob
            username={this.props.username}
            profilePhoto={this.props.profilePhoto}
            history={this.props.history}
          ></HeaderMob>
        </Responsive>
      </div>
    );
  }
}

// const mapStateToProps = state => {
//   const { navigationReducer } = state;
//   return {
//     currentTab: navigationReducer.currentTab
//   };
// };

const mapStateToProps = (state) => {
  return {
    currentTab: state.navigationReducer.currentTab,
    width: state.visibility.Width,
    profilePhoto: state.userReducer.profilePhoto,
    username: state.userReducer.username,
  };
};

export default connect(mapStateToProps, {
  setAbout,
  setRanking,
  setHome,
  setNearBy,
  setPerson,
})(Home);
