import React, { useState } from "react";
import { TextField, MenuItem, Button, Snackbar } from "@material-ui/core";
import {
  Modal,
  Form,
  TextArea,
  Button as Button1,
  Image,
  Divider,
} from "semantic-ui-react";
import { PhotoCamera, Public, SecurityTwoTone } from "@material-ui/icons";
import { postMyPost, postPhoto } from "../../../ApiRequests/request";
import { Upload, message } from "antd";
import url from "../../../strings/url";
const token = localStorage.getItem("AnonymityToken#123");
const PostProblem = (props) => {
  const [isLoading, setLoading] = useState(false);
  const [status, setStatus] = useState("unknown");
  const [description, setDescription] = useState("");
  const [isUploaded, setUploaded] = useState(false);
  const [problemImageUrl, setUrl] = useState(null);
  const [isAnonymous, setAnonymous] = useState(false);

  return (
    <div>
      {status === "error" ? (
        <Snackbar
          open={status !== "unknown"}
          message="Error Posting"
          autoHideDuration={3000}
          onClose={() => setStatus("unknown")}
        ></Snackbar>
      ) : (
        ""
      )}
      {status === "success" ? (
        <Snackbar
          open={status !== "unknown"}
          message="SuccessFul"
          autoHideDuration={3000}
          onClose={() => setStatus("unknown")}
        ></Snackbar>
      ) : (
        ""
      )}
      <Modal open={props.open} onClose={props.onClose}>
        <Modal.Header>
          <Image
            src={
              props.profilePhoto || require("../../../images/complainify.png")
            }
            size="mini"
            circular
          ></Image>
          <div
            style={{
              marginTop: 10,
              fontWeight: "500",
              fontSize: 14,
              fontFamily: "poppins",
            }}
          >
            <b>{props.UserName + " " || "Unknown" + " "}</b>
            Have a Problem <Divider></Divider>
            <div>
              <TextField
                label="Problem Should Be Listed as"
                select
                variant="filled"
                color="secondary"
                SelectProps={{
                  style: { backgroundColor: "rgba(0,0,0,0.01)" },
                }}
                fullWidth
                value={isAnonymous}
                onChange={(a, b) => {
                  setAnonymous(b.props.value);
                }}
                name="course"
              >
                <MenuItem key="Public" value={false}>
                  Public
                </MenuItem>
                <MenuItem key="Anonymous" value={true}>
                  Anonymous
                </MenuItem>
              </TextField>
            </div>
          </div>
        </Modal.Header>
        <Modal.Content>
          <Form loading={isLoading}>
            <TextArea
              style={{ height: 100, fontSize: 15, borderRadius: "0px" }}
              value={description}
              onChange={(e) => {
                setDescription(e.target.value);
              }}
              placeholder="Describe Your Problem"
            ></TextArea>
          </Form>
        </Modal.Content>
        {props.hasUpload ? (
          <Modal.Content>
            <Upload
              method="PUT"
              accept="image/*"
              headers={{
                Authorization: token,
                contentType: "application/json",
              }}
              name="image"
              action={`${url}/user/post/photo`}
              onChange={(info) => {
                if (info.file.status === "done") {
                  console.log(info.file.response);
                  setUploaded(true);
                  setLoading(false);
                  setUrl(info.file.response);
                } else if (info.file.status === "removed") {
                  setLoading(false);
                  setUploaded(false);
                } else if (info.file.status === "uploading") {
                  setLoading(true);
                } else if (info.file.error) {
                  setLoading(false);
                }
              }}
              onRemove={() => {
                console.log("on Remove called");
                const headers = new Headers();
                headers.append("Authorization", token);
                headers.append("content-type", "application/json");
                const request = new Request(
                  `${url}/post/photo/${problemImageUrl.key}`,
                  { method: "DELETE", headers }
                );
                fetch(request)
                  .then((res) => {
                    console.log(res.status);
                  })
                  .catch((e) => {
                    console.log(e);
                  });
                setUrl(null);
              }}
              disabled={isUploaded}
            >
              <Button
                style={{
                  fontFamily: "poppins",
                  fontWeight: "bold",
                  textTransform: "none",
                }}
                disabled={isUploaded}
              >
                <div style={{ marginRight: 10 }}>Upload Photo</div>
                <PhotoCamera
                  style={{ color: "rgba(44,130,201,1)" }}
                ></PhotoCamera>
              </Button>
            </Upload>
          </Modal.Content>
        ) : (
          ""
        )}

        <Modal.Actions>
          <Button1
            color="blue"
            // inverted
            size="medium"
            style={{
              marginBottom: 10,
              // color: "white",
              marginRight: 10,
              textTransform: "none",
              fontFamily: "poppins",
            }}
            disabled={description.length < 1 || isLoading}
            onClick={() => {
              setLoading(true);
              const token = localStorage.getItem("AnonymityToken#123");
              postMyPost(
                token,
                description,
                "problem",
                problemImageUrl,
                isAnonymous
              )
                .then((res) => {
                  if (res.status === 200) {
                    window.location.reload(false);
                    setStatus("success");
                    props.onClose();
                    return;
                  }
                  setLoading(false);
                  setStatus("error");
                  props.onClose();
                })
                .catch((err) => {
                  setLoading(false);
                  setStatus("error");
                });

              setUrl(null);
            }}
            variant="outlined"
          >
            Post Problem
          </Button1>
          <Button1
            size="medium"
            style={{
              marginBottom: 10,
              textTransform: "none",
              fontFamily: "poppins",
            }}
            onClick={() => {
              setLoading(false);
              props.onClose();
              if (problemImageUrl) {
                const headers = new Headers();
                headers.append("Authorization", token);
                headers.append("content-type", "application/json");
                const request = new Request(
                  `${url}/post/photo/${problemImageUrl.key}`,
                  { method: "DELETE", headers }
                );
                fetch(request)
                  .then((res) => {
                    console.log(res.status);
                    setUrl(null);
                  })
                  .catch((e) => {
                    setUrl(null);
                    console.log(e);
                  });
              }
              setUploaded(false);
            }}
            // color="red"
            // inverted
          >
            Close
          </Button1>
        </Modal.Actions>
      </Modal>
    </div>
  );
};

export default PostProblem;
