import React, { Component } from "react";
import { connect } from "react-redux";
import { CircularProgress, Button, Backdrop } from "@material-ui/core";
import { Result } from "antd";
import { isAuthRequest } from "../../ApiRequests/request";
class Authenticate extends Component {
  constructor(props) {
    super(props);
    this.state = { isLoading: true };
  }
  componentDidMount() {
    if (this.props.isAuth) {
      return this.setState({ isLoading: false });
    }
    const token = localStorage.getItem("AnonymityToken#123");
    if (token !== "false") {
      isAuthRequest(token)
        .then(async (res) => {
          if (res.status === 200) {
            let user = await res.json();
            user = { ...user, token };
            this.props.dispatch({ type: "setUser", user });
            this.props.dispatch({ type: "auth" });

            this.setState({ isLoading: false });
            return;
          }
          this.setState({ isLoading: false });
        })
        .catch((err) => {
          this.setState({ isLoading: false });
        });
    } else {
      this.setState({ isLoading: false });
    }
  }
  render() {
    return (
      <div>
        <Backdrop style={{ zIndex: 1000 }} open={this.state.isLoading}>
          <CircularProgress
            style={{ color: "black", width: 100, height: 100, zIndex: 100 }}
          ></CircularProgress>
        </Backdrop>
        {this.props.isAuth ? (
          <this.props.Component {...this.props}></this.props.Component>
        ) : this.state.isLoading ? (
          ""
        ) : (
          <Result
            status="403"
            title="403"
            subTitle="Sorry Not Authorize Please LogIn First"
            history={this.props.history}
            extra={
              <Button
                onClick={() => {
                  this.props.history.push("/login");
                }}
              >
                Go To Log In Screen
              </Button>
            }
          ></Result>
        )}
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    isAuth: state.authReducer,
  };
};
export default connect(mapStateToProps)(Authenticate);
