import React, { useState } from "react";
import { Modal, Button, ModalActions } from "semantic-ui-react";
import {
  TextField,
  ThemeProvider,
  createMuiTheme,
  CircularProgress,
} from "@material-ui/core";
import { orange } from "@material-ui/core/colors";
import url from "../../strings/url";
const theme = createMuiTheme({
  palette: {
    primary: orange,
  },
});
export const Edit = (props) => {
  const [inputText, setText] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const handleUpdate = () => {
    setLoading(true);
    const body = JSON.stringify({ [props.editField]: inputText });
    const headers = new Headers();
    headers.append("Authorization", localStorage.getItem("AnonymityToken#123"));
    headers.append("Content-Type", "application/json");
    fetch(`${url}/update/user`, { method: "PATCH", headers, body })
      .then((res) => {
        if (res.status == 200) {
          return window.location.reload();
        }
        setError(true);
      })
      .catch((e) => {
        setLoading(false);
        setError(true);
      });
  };

  return (
    <ThemeProvider theme={theme}>
      <Modal basic open={props.open} onClose={() => props.onClose()}>
        {loading ? (
          <CircularProgress style={{ color: "white" }}></CircularProgress>
        ) : (
          <TextField
            hidden={loading}
            variant="standard"
            InputProps={{ style: { color: "white" } }}
            label={`Enter New ${props.editField}`}
            InputLabelProps={{ style: { color: "white" } }}
            fullWidth
            value={inputText}
            onChange={(e) => {
              setText(e.target.value);
            }}
          ></TextField>
        )}
        <ModalActions>
          <Button
            color="black"
            onClick={() => {
              props.onClose();
              setLoading(false);
            }}
          >
            Cancel
          </Button>
          <Button
            onClick={() => {
              handleUpdate();
            }}
            color="orange"
            disabled={Boolean(inputText.length === 0)}
          >
            Update
          </Button>
        </ModalActions>
      </Modal>
    </ThemeProvider>
  );
};
