import React from "react";
import { CircularProgress, Grid } from "@material-ui/core";
import RenderProblems from "../renderProblems";
import { Pagination, Divider } from "semantic-ui-react";
import { Result } from "antd";

const ForComp = (props) => {
  const { problemsCount } = props;

  return (
    <div>
      {props.Problems.length !== 0 && !props.isLoading ? (
        <div style={{ display: "flex", justifyContent: "center" }}>
          <RenderProblems {...props}></RenderProblems>
        </div>
      ) : !props.isError && !props.isLoading ? (
        <Result
          status="info"
          title="No Problems Here! Check Again Later..."
        ></Result>
      ) : props.isLoading ? (
        ""
      ) : (
        <Result
          status="error"
          title="There's Some Error Fetching Data From The Server"
        ></Result>
      )}

      {problemsCount > 10 && !props.isLoading ? (
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            marginTop: 10,
            marginBottom: 100,
          }}
        >
          <Pagination
            size="tiny"
            firstItem={null}
            lastItem={null}
            secondary
            siblingRange={0}
            onPageChange={(event, { activePage }) => {
              props.setPageNo(activePage);
            }}
            activePage={props.pageNo}
            totalPages={Math.floor(problemsCount / 10) + 1}
            boundaryRange={1}
          ></Pagination>
        </div>
      ) : (
        <Divider style={{ marginBottom: 300 }}></Divider>
      )}

      {props.isLoading ? (
        <div
          style={{
            display: "flex",
            margin: "20%",
            justifyContent: "center",
          }}
        >
          <CircularProgress
            style={{
              width: 100,
              height: 100,
              textAlign: "center",
              color: "black",
            }}
          ></CircularProgress>
        </div>
      ) : (
        ""
      )}
    </div>
  );
};

export default ForComp;
