import React, { useState } from "react";
import {
  Grid,
  Fab,
  createMuiTheme,
  ThemeProvider,
  CircularProgress,
  Snackbar,
  Divider,
} from "@material-ui/core";
import { connect } from "react-redux";
import { Edit } from "@material-ui/icons";
import { Affix } from "antd";
import { blue, orange } from "@material-ui/core/colors";
import { Pagination } from "@material-ui/lab";
import { getMyProblems as getMyPosts } from "../../../../ApiRequests/request";
import RenderProblems from "../problemList/ReanderProblems";
import { Select } from "semantic-ui-react";
import PostProblems from "../../PostProblem/PostProblem";
const theme = createMuiTheme({
  palette: {
    primary: {
      main: orange[500],
    },
    secondary: {
      main: blue[500],
    },
  },
});
const centered = { display: "flex", justifyContent: "center" };
const sortByOptions = [
  { key: "Latest", value: "Latest", text: "Latest" },
  // { key: "UpVote", value: "UpVote", text: "Up Votes" },
  // { key: "DownVote", value: "DownVote", text: "Down Votes" },
  { key: "oldest", value: "oldest", text: "Oldest" },
];
const MyProblems = (props) => {
  const [isFetching, setFetching] = useState(true);
  const [isError, setError] = useState(false);
  const [MyProblems, setProblems] = useState([]);
  const token = localStorage.getItem("AnonymityToken#123");
  const [isFetched, setFetched] = useState(false);
  const [offset, setOffset] = useState(10);
  const [totalProblems, setCount] = useState(0);
  const [page, setPage] = useState(1);
  const [start, setStart] = useState(0);
  const [sortBy, setSortBy] = useState("Latest");
  const [postsProblem, setPost] = useState(false);

  const fetchNewProblems = (token, start, offset, sortBy) => {
    setProblems([]);
    getMyPosts(token, start, offset, sortBy)
      .then(async (res) => {
        if (res.status === 200) {
          const { problems, count } = await res.json();

          setProblems([...problems]);
          setCount(count);
          setTimeout(() => setFetching(false), 0);
          return;
        }
        setError(true);
        setTimeout(() => setFetching(false), 0);
      })
      .catch((e) => {
        setError(true);
        setTimeout(() => setFetching(false), 0);
      });
    setFetched(true);
  };
  if (!isFetched) {
    fetchNewProblems(token, start, offset);
  }

  return (
    <ThemeProvider theme={theme}>
      <PostProblems
        open={postsProblem}
        UserName={props.username}
        profilePhoto={props.profilePhoto}
        onClose={() => {
          setPost(false);
        }}
        hasUpload={true}
      ></PostProblems>
      <Snackbar
        open={isError}
        onClose={() => {
          setError(false);
        }}
        autoHideDuration={6000}
        message="Error Loading New Problems"
      ></Snackbar>
      <Grid container style={{ marginTop: 10 }}>
        <Grid item xs={3}>
          <Affix offsetTop={10}>
            <div style={centered}>
              <Fab
                color="secondary"
                style={{
                  textTransform: "none",
                  fontFamily: "poppins",
                  color: "white",
                  borderRadius: 10,
                  zIndex: 100,
                  fontWeight: "bold",
                }}
                variant="extended"
                onClick={() => {
                  setPost(true);
                }}
              >
                <Edit></Edit>
                Add Complaint
              </Fab>
            </div>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                marginTop: 20,
                fontWeight: "bold",
                fontSize: 15,
                fontFamily: "poppins",
              }}
            >
              SortBy
            </div>
            <div style={{ ...centered, marginTop: 5 }}>
              <Select
                placeholder="Sort Problem By ?"
                options={sortByOptions}
                value={sortBy}
                onChange={(e, { value }) => {
                  setSortBy(value);

                  setFetching(true);
                  setPage(1);
                  fetchNewProblems(token, 0, offset, value);
                }}
              ></Select>
            </div>
          </Affix>
        </Grid>
        <Divider orientation="vertical" flexItem></Divider>
        <Grid item xs={6} style={centered}>
          <div>
            <div style={{ display: "flex", justifyContent: "center" }}>
              <RenderProblems
                isLoading={isFetching}
                problems={MyProblems}
                {...props}
              ></RenderProblems>
            </div>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                marginTop: 100,
                marginBottom: 100,
              }}
            >
              {isFetching ? (
                <div
                  style={{
                    display: "flex",
                    justifyContent: "center",
                    marginTop: 100,
                    marginBottom: 100,
                  }}
                >
                  <CircularProgress
                    style={{ width: 50, height: 50, color: "black" }}
                  ></CircularProgress>
                </div>
              ) : (
                <div
                  style={{
                    display: "flex",
                    justifyContent: "center",
                    marginTop: 50,
                    marginBottom: 50,
                  }}
                >
                  {totalProblems > 10 ? (
                    <Pagination
                      color="secondary"
                      page={page}
                      onChange={(event, page) => {
                        setFetching(true);
                        fetchNewProblems(
                          token,
                          (page - 1) * offset,
                          offset,
                          sortBy
                        );
                        window.scrollTo(0, 0);
                        setPage(page);
                      }}
                      count={parseInt(totalProblems / offset) + 1}
                    ></Pagination>
                  ) : (
                    ""
                  )}
                </div>
              )}
            </div>
          </div>
        </Grid>
        <Divider orientation="vertical" flexItem></Divider>
      </Grid>
    </ThemeProvider>
  );
};
const mapStateToProps = (state) => {
  return {
    username: state.userReducer.username,
    profilePhoto: state.userReducer.profilePhoto,
  };
};
export default connect(mapStateToProps)(MyProblems);
