import React from "react";
import Problem from "../../Home/MyProblems/problemList/Problem";

const RenderList = (props) => {
  return (
    <div>
      {props.Problems
        ? props.Problems.map((problem) => {
            const UpVotedByMe = problem.UpVoted_By.find((user) => {
              return user.user === props.id;
            });
            const DownVotedByMe = problem.DownVoted_By.find((user) => {
              return user.user === props.id;
            });

            return (
              <Problem
                deviceType={props.deviceType}
                key={problem._id}
                UpVotedByMe={!!UpVotedByMe}
                DownVotedByMe={!!DownVotedByMe}
                UserId={problem.Author._id}
                id={problem._id}
                profilePhoto={
                  problem.Anonymous
                    ? "https://upload.wikimedia.org/wikipedia/commons/thumb/e/e0/Anonymous.svg/433px-Anonymous.svg.png"
                    : problem.Author.profilephoto
                }
                username={
                  problem.Anonymous ? "Anonymous" : problem.Author.username
                }
                anonymous={problem.Anonymous}
                header={"College Problems"}
                image={!problem.Media ? "" : problem.Media.imageUrl}
                description={problem.Description}
                createdAt={problem.createdAt}
                UpVotes={problem.UpVotes}
                DownVotes={problem.DownVotes}
                hasDeleteButton={false}
                history={props.history}
              ></Problem>
            );
          })
        : ""}
    </div>
  );
};

export default RenderList;
