import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import AuthHeader from "../authHeader/Header";
import "../../animation.css";
import Login from "../Login/Login";
import { connect } from "react-redux";
import Signup from "../SignUp/signup";
import HomeNav from "../HomeNavigator/homenav";
// import NearBy from "../NearBy/Problems/problems";
import NotFound from "../NotFound/Not";
import Auth from "../AuthenticateComponent/Auth";
import MyProblems from "../Home/MyProblems/MyProblems";
import NearByProblems from "../NearBy/Problems/problems";
import MyProfile from "../Person/MyProfile/MyProfile";
import Others from "../Person/OthersProfile/OthersProfile";
import { Notes } from "../Notes/Notes";
import { About } from "../About/About";
import Cropper from "../imagecropper/IC";
import { IU } from "../imagecropper/ImageUploader/IU";
import NearByNav from "../NearByNavigator/NearByNav";
import SensitiveProblems from "../NearBy/SensitiveProblem";
const AuthMyProblems = (props) => {
  return <Auth {...props} Component={MyProblems}></Auth>;
};
const AuthNearByProblems = (props) => {
  return <Auth {...props} Component={NearByProblems}></Auth>;
};
const AuthMyProfile = (props) => {
  return <Auth {...props} Component={MyProfile}></Auth>;
};
const AuthOthers = (props) => {
  return <Auth {...props} Component={Others}></Auth>;
};
const AuthSensitive = (props) => {
  return <Auth {...props} Component={SensitiveProblems}></Auth>;
};
const Router = () => {
  const AuthenticatedRoutes = (
    <div>
      <Route path="/anonymity" component={AuthHeader}></Route>
      <Route path="/anonymity/Home" component={HomeNav}></Route>
      <Route path="/anonymity/nearby" component={NearByNav}></Route>
      <Switch>
        <Route
          exact
          path="/anonymity/nearby/sensitive"
          component={AuthSensitive}
        ></Route>
        <Route
          exact
          path="/anonymity/Nearby"
          component={AuthNearByProblems}
        ></Route>
        <Route exact path="/" component={Login}></Route>
        <Route exact path="/signup" component={Signup}></Route>
        <Route exact path="/loginA" component={Login}></Route>
        <Route exact path="/login" component={Login}></Route>
        <Route
          exact
          path="/anonymity/Home/MyProblems"
          component={AuthMyProblems}
        ></Route>
        <Route exact path="/anonymity/person" component={AuthMyProfile}></Route>
        <Route
          exact
          path="/anonymity/person/:id"
          component={AuthOthers}
        ></Route>
        <Route exact path="/anonymity/Home" component={AuthMyProblems}></Route>
        <Route exact path="/anonymity/Home/Notes" component={Notes}></Route>
        <Route exact path="/anonymity/About" component={About}></Route>
        <Route exact path="/anonymity/Cropper" component={Cropper}></Route>
        <Route exact path="/anonymity/imageUpload" component={IU}></Route>
        <Route path="" component={NotFound}></Route>
      </Switch>
    </div>
  );

  return <BrowserRouter>{AuthenticatedRoutes}</BrowserRouter>;
};

const mapStateToProps = (state) => {
  return {
    isAuth: state.authReducer,
  };
};

export default connect(mapStateToProps)(Router);
