import React, { Fragment, useState } from "react";
import {
  Button,
  CircularProgress,
  Fab,
  Popper,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Checkbox,
} from "@material-ui/core";
import {
  ExpandLessOutlined,
  ExpandMoreOutlined,
  HighlightOffOutlined,
  LowPriorityOutlined,
} from "@material-ui/icons";
import NotAuth from "./NotAuthorize";
import { CenterStuff } from "../../Person/OthersProfile/profile/Profile";
import { Responsive } from "semantic-ui-react";
import ReanderProblems from "../Problems/renderProblems";
import { getSensitiveContent } from "../../../ApiRequests/request";
import { Pagination } from "@material-ui/lab";

const SortByOptions = [
  {
    by: "Latest",
    value: { sortBy: { _id: -1 }, name: "Latest" },
    icon: <ExpandLessOutlined></ExpandLessOutlined>,
  },
  {
    by: "Oldest",
    value: { sortBy: { _id: 1 }, name: "Oldest" },
    icon: <ExpandMoreOutlined></ExpandMoreOutlined>,
  },
  {
    by: "High Nude Probability",
    value: { sortBy: { nudeProb: 1 }, name: "HighN" },
    icon: <HighlightOffOutlined></HighlightOffOutlined>,
  },
  {
    by: "Low Nude Probability",
    value: { sortBy: { nudeProb: -1 }, name: "LowN" },
    icon: <LowPriorityOutlined></LowPriorityOutlined>,
  },
];

const SensitiveProblems = (props) => {
  const [isAuth, setAuth] = useState("unknown");
  const [isLoading, setLoading] = useState(false);
  const [problems, setProblems] = useState([]);
  const [error, setError] = useState(null);
  const [sortBy, setSortBy] = useState({ sortBy: { _id: -1 }, name: "Latest" });
  const [filter, setFilter] = useState(null);
  const [totalPageCount, setTotalPageCount] = useState(0);
  const [page, setPage] = useState(1);
  const [anchorEl, setAnchorEl] = useState(null);

  const handleComplaints = async (skip, options) => {
    setLoading(true);
    try {
      const resp = await getSensitiveContent(skip, options);
      if (resp.status === 401) {
        setAuth(false);
        setLoading(false);
      } else {
        setAuth(true);
        const cc = await resp.json();
        setProblems(cc.problems);
        setTotalPageCount(parseInt(cc.count / 10 + 1));
        setLoading(false);
      }
    } catch (e) {
      setLoading(false);
      setError("Internal Server Error");
    }
  };
  return (
    <Fragment>
      {isLoading ? (
        <CenterStuff style={{ marginTop: 80 }}>
          <CircularProgress style={{ color: "black" }}></CircularProgress>
        </CenterStuff>
      ) : (
        ""
      )}
      {isAuth === false ? (
        <NotAuth></NotAuth>
      ) : isAuth === "unknown" && !isLoading ? (
        <Fragment>
          <div
            style={{
              fontFamily: "poppins",
              fontWeight: "bold",
              fontSize: 15,
              textAlign: "center",
              marginTop: 30,
              padding: 10,
            }}
          >
            This Section Consists Of Complaints Whose Likelihood of Consisting
            Nude Content is Higher Than 50% So Click On The Button At Your Own
            Risk
          </div>
          <div
            style={{ display: "flex", justifyContent: "center", marginTop: 30 }}
          >
            <Button
              variant="outlined"
              onClick={() => {
                handleComplaints(0);
              }}
            >
              View Complaints Anyway
            </Button>
          </div>
        </Fragment>
      ) : (
        ""
      )}

      {!isLoading && isAuth === true ? (
        <Fragment>
          <Popper
            open={Boolean(anchorEl)}
            anchorEl={anchorEl}
            placement={"top-end"}
          >
            <List
              component="nav"
              style={{
                boxShadow: "0px 0px 10px rgba(0,0,0,0.1)",
                width: 300,
                transform: "translateY(-10px)",
                background: "rgba(255,255,255,1)",
                borderRadius: 10,
                overflowY: "auto",
                maxHeight: 450,
              }}
            >
              <ListItem>
                <ListItemText
                  style={{ color: "black", fontWeight: "bold" }}
                  primary="Sort By"
                  secondary="Something"
                ></ListItemText>
              </ListItem>

              <List component="nav" disablePadding>
                {SortByOptions.map((option) => {
                  return (
                    <ListItem
                      button
                      onClick={(e) => {
                        if (sortBy.name !== option.value.name) {
                          setSortBy(option.value);
                          handleComplaints(0, { sortBy: option.value });
                          setPage(1);
                          setAnchorEl(null);
                        }
                      }}
                      key={option.by}
                    >
                      <ListItemIcon>{option.icon}</ListItemIcon>
                      <ListItemText primary={option.by}></ListItemText>
                      <Checkbox
                        onChange={(e) => {
                          setSortBy(option.value);
                        }}
                        checked={sortBy.name === option.value.name}
                      ></Checkbox>
                    </ListItem>
                  );
                })}
              </List>
            </List>
          </Popper>
          <Fab
            style={{
              background: "white",
              position: "fixed",
              right: "2%",
              bottom: "10%",
              zIndex: 1000,
            }}
            onClick={(e) => {
              if (!Boolean(anchorEl)) {
                return setAnchorEl(e.currentTarget);
              }
              setAnchorEl(null);
            }}
          >
            <img
              style={{ width: 30, height: 30 }}
              src="https://img.icons8.com/color/50/000000/plus-math.png"
            />
          </Fab>
          <AuthSensitive
            problems={problems}
            history={props.history}
          ></AuthSensitive>

          <CenterStuff style={{ marginTop: 100, marginBottom: 300 }}>
            <Pagination
              color="secondary"
              count={totalPageCount}
              page={page}
              onChange={(e, page) => {
                setPage(page);
                handleComplaints((page - 1) * 10);
              }}
            ></Pagination>
          </CenterStuff>
        </Fragment>
      ) : (
        ""
      )}
    </Fragment>
  );
};

const AuthSensitive = (props) => {
  return (
    <Fragment>
      <Responsive {...Responsive.onlyComputer}>
        <ComplaintsSS
          deviceType={"comp"}
          history={props.history}
          problems={props.problems}
        ></ComplaintsSS>
      </Responsive>
      <Responsive {...Responsive.onlyTablet}>
        <ComplaintsSS
          deviceType={"tab"}
          history={props.history}
          problems={props.problems}
        ></ComplaintsSS>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        <ComplaintsSS
          deviceType={"mob"}
          history={props.history}
          problems={props.problems}
        ></ComplaintsSS>
      </Responsive>
    </Fragment>
  );
};

const ComplaintsSS = (props) => {
  return props.problems.length > 0 ? (
    <CenterStuff>
      <ReanderProblems
        Problems={props.problems}
        history={props.history}
        deviceType={props.deviceType}
      ></ReanderProblems>
    </CenterStuff>
  ) : (
    <Fragment>
      <CenterStuff style={{ marginTop: 60 }}>
        <img src="https://img.icons8.com/color/96/000000/nothing-found.png" />
      </CenterStuff>
      <div
        style={{
          fontSize: 20,
          color: "black",
          fontWeight: "bold",
          textAlign: "center",
          fontFamily: "poppins",
        }}
      >
        No Complaints...
      </div>
    </Fragment>
  );
};

export default SensitiveProblems;
