import React, { useState, Fragment } from "react";
import { connect } from "react-redux";
import ImageCropper from "../../../imagecropper/IC";
import {
  Paper,
  Avatar,
  List,
  ListItem,
  IconButton,
  ListSubheader,
  Modal,
  Typography,
  Divider,
  CircularProgress,
} from "@material-ui/core";
import { Skeleton } from "@material-ui/lab";

import { Image } from "semantic-ui-react";
import { Responsive } from "semantic-ui-react";
import { grey } from "@material-ui/core/colors";
import { getSomeoneProblems } from "../../../../ApiRequests/request";
import { Visibility, Divider as Divider1 } from "semantic-ui-react";
import RenderProblems from "../../../NearBy/Problems/renderProblems";
export const CenterStuff = (props) => {
  return (
    <div style={{ display: "flex", justifyContent: "center", ...props.style }}>
      {props.children}
    </div>
  );
};

const RenderOtherProblems = (props) => {
  let { complaints, isLoading, deviceType, history } = props;
  complaints = complaints.map((complaint) => {
    return {
      ...complaint,
      Author: { username: props.username, profilephoto: props.profilephoto },
    };
  });
  return (
    <div>
      <CenterStuff>
        <RenderProblems
          id={props.id}
          history={history}
          Problems={complaints}
          deviceType={deviceType}
        ></RenderProblems>
      </CenterStuff>
      <CenterStuff style={{ marginTop: 30, marginBottom: 30 }}>
        {isLoading ? (
          <CircularProgress></CircularProgress>
        ) : complaints.length === 0 ? (
          <div
            style={{ fontFamily: "poppins", fontWeight: "bold", fontSize: 16 }}
          >
            Maybe Have Lot of Anonymous Complaints
          </div>
        ) : (
          <div
            style={{ fontFamily: "poppins", fontWeight: "bold", fontSize: 16 }}
          >
            End of His Complaints
          </div>
        )}
      </CenterStuff>
    </div>
  );
};

const Profile = (props) => {
  const [complaints, setComplaints] = useState([]);

  const [CL, setCL] = useState(true);
  const [skip, setSkip] = useState(0);
  const handleGP = () => {
    setCL(true);
    getSomeoneProblems(props.userId, skip)
      .then(async (res) => {
        if (res.ok) {
          const a = await res.json();

          setComplaints([...complaints, ...a]);
          setCL(false);
          setSkip(skip + 10);
          return;
        }
        setCL(false);
      })
      .catch((e) => {
        setCL(false);
      });
  };

  return (
    <Visibility
      onUpdate={(e, { calculations }) => {
        if (calculations.bottomVisible && complaints.length > skip - 10) {
          handleGP();
        }
      }}
    >
      <div>
        <Responsive {...Responsive.onlyComputer}>
          <ProfileInfo
            editable={props.editable}
            {...props}
            deviceType={"com"}
          ></ProfileInfo>
          <RenderOtherProblems
            id={props.id}
            history={props.history}
            profilephoto={props.profilephoto}
            username={props.username}
            deviceType={"comp"}
            complaints={complaints}
            isLoading={CL}
          ></RenderOtherProblems>
        </Responsive>
        <Responsive {...Responsive.onlyTablet}>
          <ProfileInfo
            editable={props.editable}
            {...props}
            deviceType={"tab"}
          ></ProfileInfo>
          <RenderOtherProblems
            profilephoto={props.profilephoto}
            id={props.id}
            history={props.history}
            username={props.username}
            deviceType={"tab"}
            complaints={complaints}
            isLoading={CL}
          ></RenderOtherProblems>
        </Responsive>
        <Responsive {...Responsive.onlyMobile}>
          <ProfileInfo
            editable={props.editable}
            {...props}
            deviceType={"mob"}
          ></ProfileInfo>
          <RenderOtherProblems
            history={props.history}
            profilephoto={props.profilephoto}
            id={props.id}
            username={props.username}
            deviceType={"mob"}
            complaints={complaints}
            isLoading={CL}
          ></RenderOtherProblems>
        </Responsive>
      </div>
    </Visibility>
  );
};
const ListHeaderStyle = {
  fontFamily: "poppins",
  fontWeight: "bold",
  color: "grey",
  fontSize: 16,
};
const ImageModal = (props) => {
  const { editable, src, open, setModal, deviceType } = props;
  const [ImageSrc, setSrc] = useState(null);
  const modalSize = (deviceType) => {
    switch (deviceType) {
      case "com": {
        return {
          maxWidth: 600,
          maxHeight: 600,
        };
      }
      case "tab": {
        return {
          maxWidth: 600,
          maxHeight: 600,
        };
      }
      case "mob": {
        return {
          maxWidth: 325,
          maxHeight: 325,
        };
      }
    }
  };

  return (
    <Fragment>
      <ImageCropper
        src={ImageSrc}
        pp={props.pp}
        onClose={() => {
          setSrc(null);
        }}
      ></ImageCropper>
      <Modal
        open={open}
        onClose={() => {
          setModal(false);
        }}
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <Paper style={{ background: grey[900] }}>
          <Image src={src} size="huge" style={modalSize(deviceType)}></Image>
          <input
            type="file"
            style={{ display: "none" }}
            accept="image/*"
            id="photoUploadInput"
            onChange={(e) => {
              const reader = new FileReader();
              reader.addEventListener("load", () => {
                setSrc(reader.result);
                setModal(false);
              });
              reader.readAsDataURL(e.target.files[0]);
            }}
          ></input>
          {editable ? (
            <List>
              <label htmlFor="photoUploadInput">
                <ListItem button style={ListHeaderStyle}>
                  <img
                    src="https://img.icons8.com/color/48/000000/edit-image.png"
                    style={{ maxWidth: 30, maxHeight: 30 }}
                  />
                </ListItem>
              </label>
            </List>
          ) : (
            ""
          )}
        </Paper>
      </Modal>
    </Fragment>
  );
};

export const ProfileInfo = (props) => {
  const [openModal, setOpenModal] = useState(false);
  const [imgSrc, setSrc] = useState("pp");
  const [ppl, setPPl] = useState(false);
  const [bpl, setBPl] = useState(false);
  const calculateWidth = (type) => {
    switch (type) {
      case "com":
        return {
          width: 700,
        };
      case "tab":
        return {
          width: 500,
        };
      case "mob":
        return {
          width: 350,
        };
      default:
        return {
          width: 500,
        };
    }
  };
  const editIconTranslate = "translateY(-40px)";
  const dividerBack = grey[800];
  return (
    <div>
      <ImageModal
        open={openModal}
        editable={props.editable}
        setModal={setOpenModal}
        deviceType={props.deviceType}
        pp={imgSrc === "pp"}
        src={imgSrc === "pp" ? props.profilephoto : props.backgroundPhoto}
      ></ImageModal>
      <Paper
        elevation={10}
        style={{
          ...calculateWidth(props.deviceType),
          borderRadius: 10,
          background: grey[900],
        }}
      >
        <Skeleton
          hidden={bpl}
          variant="rect"
          width={"100%"}
          style={{
            color: "white",
            background: "grey",
            borderTopLeftRadius: 10,
            borderTopRightRadius: 10,
          }}
          height={300}
        ></Skeleton>
        <Image
          hidden={!bpl}
          src={props.backgroundPhoto}
          style={{
            width: "100%",
            maxHeight: calculateWidth(props.deviceType),
            borderTopLeftRadius: 10,
            borderTopRightRadius: 10,
            cursor: "pointer",
          }}
          onClick={() => {
            setOpenModal(true);
            setSrc("bp");
          }}
          onLoad={() => {
            setBPl(true);
          }}
        ></Image>

        <Skeleton
          hidden={ppl}
          variant="circle"
          width={100}
          height={100}
          style={{
            color: "white",
            background: "lightgrey",
            transform: "translateY(-50px) translateX(20px)",
          }}
        ></Skeleton>

        <Avatar
          hidden={!ppl}
          src={props.profilephoto}
          style={{
            width: 100,
            height: 100,
            transform: "translateY(-50px) translateX(20px)",
            cursor: "pointer",
          }}
          onClick={() => {
            setOpenModal(true);
            setSrc("pp");
          }}
          onLoad={() => {
            setPPl(true);
          }}
        ></Avatar>

        <List style={{ marginLeft: 10, marginRight: 10 }}>
          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: "bold",
              fontFamily: "poppins",
              transform: "translateY(-60px)",
              color: "white",
            }}
          >
            {props.username}
          </ListSubheader>

          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: 500,
              color: "grey",
              fontFamily: "poppins",
            }}
          >
            Name
            <Typography
              style={{
                color: "white",
                marginLeft: 10,
                fontFamily: "poppins",
              }}
            >
              {props.firstName + " " + props.LastName}
            </Typography>
            {props.editable ? (
              <IconButton
                style={{
                  float: "right",
                  color: "white",
                  transform: editIconTranslate,
                }}
                onClick={() => {
                  props.openEdit();
                  props.setType("name");
                }}
              >
                <img
                  src="https://img.icons8.com/color/48/000000/edit.png"
                  style={{ maxWidth: 30, maxHeight: 30 }}
                />
              </IconButton>
            ) : (
              ""
            )}
          </ListSubheader>
          <Divider
            style={{
              color: "grey",
              background: dividerBack,
              marginTop: 10,
            }}
          ></Divider>
          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: 500,
              color: "grey",
              fontFamily: "poppins",
            }}
          >
            Gender
            <Typography
              style={{
                color: "white",
                marginLeft: 10,
                fontFamily: "poppins",
              }}
            >
              {props.gender}
            </Typography>
          </ListSubheader>
          <Divider
            style={{
              color: "lightgrey",
              background: dividerBack,
              marginTop: 10,
            }}
          ></Divider>
          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: 500,
              color: "grey",
              fontFamily: "poppins",
            }}
          >
            Course
            <Typography
              style={{
                color: "white",
                marginLeft: 10,
                fontFamily: "poppins",
              }}
            >
              {props.course}
            </Typography>
          </ListSubheader>
          <Divider
            style={{
              color: "lightgrey",
              background: dividerBack,
              marginTop: 10,
            }}
          ></Divider>
          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: 500,
              color: "grey",
              fontFamily: "poppins",
            }}
          >
            Stream
            <Typography
              style={{
                color: "white",
                marginLeft: 10,
                fontFamily: "poppins",
              }}
            >
              {props.Branch}
            </Typography>
          </ListSubheader>

          <Divider
            style={{
              color: "lightgrey",
              background: dividerBack,
              marginTop: 10,
            }}
          ></Divider>
          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: 500,
              color: "grey",
              fontFamily: "poppins",
            }}
          >
            Roll_No
            <Typography
              style={{
                color: "white",
                marginLeft: 10,
                fontFamily: "poppins",
              }}
            >
              {props.rollno}
            </Typography>
          </ListSubheader>
          <Divider
            style={{
              color: "lightgrey",
              background: dividerBack,
              marginTop: 10,
            }}
          ></Divider>
          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: 500,
              color: "grey",
              fontFamily: "poppins",
            }}
          >
            College
            <Typography
              style={{
                color: "white",
                marginLeft: 10,
                marginBottom: 10,
                fontFamily: "poppins",
              }}
            >
              {props.college}
            </Typography>
          </ListSubheader>
          <Divider
            style={{
              color: "lightgrey",
              background: dividerBack,
              marginTop: 10,
            }}
          ></Divider>
          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: 500,
              color: "grey",
              fontFamily: "poppins",
            }}
          >
            Year Of Joining College
            <Typography
              style={{
                color: "white",
                marginLeft: 10,
                marginBottom: 10,
                fontFamily: "poppins",
              }}
            >
              {props.yearofjoining}
            </Typography>
          </ListSubheader>

          {props.children}
        </List>
      </Paper>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    id: state.userReducer.id,
  };
};

export default connect(mapStateToProps)(Profile);
