// export default CropComponent;
export default class ProfilePhotoUploader extends PureComponent {
  state = {
    src: null,
    crop: {
      unit: "%",
      width: 100,
      aspect: this.props.pp ? 1 : 16 / 9,
    },
    loading: false,
    error: false,
  };
  componentDidMount() {
    this.setState({
      crop: {
        unit: "%",
        width: 100,
        aspect: this.props.pp ? 1 : 16 / 9,
      },
    });
  }

  onSelectFile = (e) => {
    if (e.target.files && e.target.files.length > 0) {
      const reader = new FileReader();
      reader.addEventListener("load", () => {
        this.setState({ src: reader.result });
      });
      reader.reÌ¥adAsDataURL(e.target.files[0]);
    }
  };

  // If you setState the crop in here you should return false.
  onImageLoaded = (image) => {
    this.imageRef = image;
  };

  onCropComplete = (crop) => {
    this.makeClientCrop(crop);
  };

  onCropChange = (crop, percentCrop) => {
    // You could also use percentCrop:
    // this.setState({ crop: percentCrop });
    this.setState({ crop });
  };

  async makeClientCrop(crop) {
    if (this.imageRef && crop.width && crop.height) {
      const croppedImage = await this.getCroppedImg(
        this.imageRef,
        crop,
        "newFile.jpeg"
      );
      this.setState({ croppedImage });
    }
  }

  getCroppedImg(image, crop, fileName) {
    const canvas = document.createElement("canvas");
    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;
    canvas.width = crop.width;
    canvas.height = crop.height;
    const ctx = canvas.getContext("2d");

    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width,
      crop.height
    );

    return new Promise((resolve, reject) => {
      canvas.toBlob((blob) => {
        if (!blob) {
          reject(new Error("Canvas is empty"));
          return;
        }
        blob.name = fileName;
        resolve(blob);
      }, "image/jpeg");
    });
  }

  render() {
    const { crop, croppedImage } = this.state;

    return (
      <Fragment>
        <Snackbar
          open={this.state.error}
          anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
          autoHideDuration={6000}
          onClose={() => {
            this.setState({ error: false });
          }}
          message="Error Uploading Image"
        ></Snackbar>
        <Modal
          open={Boolean(this.props.src)}
          basic
          onClose={() => {
            this.props.onClose();
          }}
          closeIcon={true}
        >
          <CenterStuff>
            <ReactCrop
              src={this.props.src}
              crop={{ ...crop, aspect: this.props.pp ? 1 : 16 / 9 }}
              ruleOfThirds
              onImageLoaded={this.onImageLoaded}
              onComplete={this.onCropComplete}
              onChange={this.onCropChange}
            />
          </CenterStuff>

          <ModalActions>
            <ThemeProvider
              theme={createMuiTheme({ palette: { primary: orange } })}
            >
              {this.state.loading ? (
                <CircularProgress></CircularProgress>
              ) : (
                <Button
                  variant="contained"
                  disableElevation
                  color="primary"
                  onClick={() => {
                    this.setState({ loading: true });
                    const fd = new FormData();
                    const headers = new Headers();
                    headers.append(
                      "Authorization",
                      localStorage.getItem("AnonymityToken#123")
                    );

                    fd.append("image", croppedImage, "whatever");
                    fetch(`${url}/user/upload/photo`, {
                      method: "PUT",
                      body: fd,
                      headers,
                    })
                      .then(async (res) => {
                        if (res.status === 200) {
                          const resbody = await res.json();
                          console.log(resbody);
                          const body = JSON.stringify({
                            photo: resbody.imageUrl,
                          });
                          headers.append("content-type", "application/json");
                          fetch(
                            `${url}/user/update/${
                              this.props.pp ? "profilePhoto" : "backgroundPhoto"
                            }`,
                            { method: "PATCH", body, headers }
                          )
                            .then((pres) => {
                              if (pres.status === 200) {
                                return window.location.reload();
                              }
                              this.setState({ error: true, loading: false });
                            })
                            .catch((e) => {
                              this.setState({ error: true, loading: false });
                            });
                        } else {
                          this.setState({ error: true, loading: false });
                        }
                      })
                      .catch((e) => {
                        this.setState({ error: true, loading: false });
                      });
                  }}
                >
                  Upload
                </Button>
              )}
            </ThemeProvider>
          </ModalActions>
        </Modal>
      </Fragment>
    );
  }
}
